export function setElementErrorMessage(id, message) {
  message === '' ? document.getElementById(id).style.display = 'none' : document.getElementById(id).style.display = 'block';
  document.getElementById(id).innerText = message;
}

export function removeLastCharacterFromString(str) {
  return (str.substring(0, str.length - 1));
}

export function removeDOMElement(selector) {
  const element = window.document.querySelector(selector);
  if (element !== null) {
    element.parentNode.removeChild(element);
  }
}

export function getComplianceData(getComplianceDetails, paymentMethodResponse, locale) {
  try {
    return getComplianceDetails(paymentMethodResponse.sessionInformation.sellingEntityId, locale, paymentMethodResponse.sessionInformation.country, true);
  } catch (e) {
    return undefined;
  }
}