export function getDropinCheckout(sessionId, apiKey, fingerprint) {
  return fetch('https://api.digitalriver.com/drop-in/checkout-sessions/' + sessionId, {headers: {Authorization: 'bearer ' + apiKey, fingerprint: fingerprint}});
}

export function cloneDropinCheckout(sessionId, fingerprint, apiKey) {
  return fetch('https://api.digitalriver.com/drop-in/checkout-sessions/' + sessionId + '/clone', {method: 'POST', body: JSON.stringify({}), headers: {'Content-Type': 'application/json', Accept: 'application/json', Authorization: 'bearer ' + apiKey, fingerprint: fingerprint}});
}

export function attachSourceToDropinCheckout(sessionId, sourceId, fingerprint, apiKey, saveForLater = false) {
  return fetch('https://api.digitalriver.com/drop-in/checkout-sessions/' + sessionId + '/sources/' + sourceId + '?saveForLater=' + saveForLater, {method: 'POST', body: JSON.stringify({}), headers: {'Content-Type': 'application/json', Accept: 'application/json', Authorization: 'bearer ' + apiKey, fingerprint: fingerprint}});
}

export function detachSourceToDropinCheckout(sessionId, sourceId, fingerprint, apiKey) {
  return fetch('https://api.digitalriver.com/drop-in/checkout-sessions/' + sessionId + '/sources/' + sourceId, {method: 'DELETE', headers: {'Content-Type': 'application/json', Accept: 'application/json', Authorization: 'bearer ' + apiKey, fingerprint: fingerprint}});
}

export function attachStoreCreditToDropinCheckout(sessionId, upstreamId, fingerprint, apiKey) {
  return fetch(`https://api.digitalriver.com/drop-in/checkout-sessions/${sessionId}/store-credit/${upstreamId}`, {method: 'POST', headers: {'Content-Type': 'application/json', Accept: 'application/json', Authorization: 'bearer ' + apiKey, fingerprint: fingerprint}});
}

