export function createDropinCheckoutOrder(sessionId, fingerprint, apiKey) {
  return fetch('https://api.digitalriver.com/drop-in/orders', {method: 'POST', body: JSON.stringify({ checkoutSessionId: sessionId }), headers: {'Content-Type': 'application/json', Accept: 'application/json', Authorization: 'bearer ' + apiKey, fingerprint: fingerprint}});
}

export function uploadFile(file, sessionId, fingerprint, apiKey) {
  return fetch('https://api.digitalriver.com/drop-in/checkout-sessions/' + sessionId + '/files', {method: 'POST', body: JSON.stringify(file), headers: {'Content-Type': 'application/json', Accept: 'application/json', Authorization: 'bearer ' + apiKey, fingerprint: fingerprint}});
}



