import {paymentServiceGetRequest, paymentServiceRequest} from '../../payment-service-request';
import {config} from '../config';
import {formatComponentTriggerErrors} from './controller-create-source-utils';

export function runGetOfflineRefundInfoHandleResponse(apiKey, refundToken) {
  return paymentServiceGetRequest(apiKey, `${config.paymentServiceBaseUrl}/refund-schemas/${refundToken}`)
    .then((response) => {
      return response.data;
    })
    .catch(() => {
      return [
      ];
    });
}

export function getRequestFromComponentData(componentData) {
  const data = [];
  componentData.forEach( field => {
    data.push({
      name: field.type,
      value: field.data
    });
  });
  return {
    refundSchemaItems: data
  };
}

export function runPostSchemasHandleResponse(apiKey, refundToken, components) {

  const offlineRefundComponent = components['offlinerefund'];

  return offlineRefundComponent.send('getComponentData'+offlineRefundComponent.id, {}).then((response) => {
    const allComponentsData = response.data;

    const triggerEventPromises = allComponentsData.map(componentData => components[componentData.id].send('componentTriggerEvent', {type: 'createSource'}));

    return Promise.all(triggerEventPromises).then( triggerValues => {

      const errors = formatComponentTriggerErrors(triggerValues);

      if (errors.length > 0) {
        return Promise.reject({
          errorCode: 'REQUIRED_FIELDS_NOT_ENTERED'
        });
      }

      const getDataPromises = allComponentsData.map( componentData => components[componentData.id]
        .send('getComponentData', {})
        .then( (source) => {
          return {
            data: source.data,
            type: componentData.type
          };
        }));

      return Promise.all(getDataPromises).then(data => {

        const request = getRequestFromComponentData(data);

        return paymentServiceRequest(request, apiKey, `${config.paymentServiceBaseUrl}/refund-schemas/${refundToken}`)
          .then((response) => {
            return response.data;
          });
      });
    });

  });


}