import {appendCompliance} from './Compliance';
import {waitForRequiredData} from '../utils';


function buildLinks(complianceData) {
  const links = [
    {
      name: complianceData.privacyPolicy.localizedText,
      url: complianceData.privacyPolicy.url
    },
    {
      name: complianceData.termsOfSale.localizedText,
      url: complianceData.termsOfSale.url
    },
    {
      name: complianceData.cookiePolicy.localizedText,
      url: complianceData.cookiePolicy.url
    },
    {
      name: complianceData.legalNotice.localizedText,
      url: complianceData.legalNotice.url
    }
  ];

  if (complianceData.californiaPrivacyRights) {
    links.push({
      name: complianceData.californiaPrivacyRights.localizedText,
      url: complianceData.californiaPrivacyRights.url
    });
  }

  if (complianceData.warrantyInformation) {
    links.push({
      name: complianceData.warrantyInformation.localizedText,
      url: complianceData.warrantyInformation.url
    });
  }
  return links;
}

export function compliance(key, configuration, storage, getDetails) {

  let nodeToKeep;

  const mountedVersion = {
    entity: undefined
  };

  const callApiAndMount = async function () {

    if (storage?.checkoutData?.sellingEntity?.id && mountedVersion.entity !== storage?.checkoutData?.sellingEntity?.id ) {

      const complianceData = getDetails(storage?.checkoutData?.sellingEntity?.id, storage.locale).disclosure;

      const links = buildLinks(complianceData);

      appendCompliance(nodeToKeep, {
        links: links
      });

    }

  };

  return {
    mount: (node) => {

      if (typeof node === 'string') {
        node = document.getElementById(node);
      }

      if (!(node instanceof HTMLElement)) {
        throw new Error('mount() requires a valid HTMLElement or id attribute.');
      }

      nodeToKeep = node;

      waitForRequiredData(storage).then( () => {
        callApiAndMount();
      });

      storage.subscribers.push(callApiAndMount);


    }

  };

}