/**
 * handleSingleError resolves a promise with unknown errors failure
 * @param {string} error
 * @param {string} type
 * @returns {Promise<{error: {type: string, errors: {code: *, message: *}[]}}>}
 */
export function handleSingleError(error, type) {
  return Promise.resolve({
    error: {
      type: 'unknown' ,
      errors: [ {code: type, message: error } ]
    }
  });
}

/**
 * Resolves a promise with an error when there is a 401 response
 * @returns {Promise<{error: {type: string, errors: {message: string}[]}}>}
 */
export function handleUnauthorizedError() {
  return Promise.resolve({
    error: {
      type: 'unauthorized',
      errors: [ {message: 'An authorization error has occurred.' } ]
    }
  });
}

/**
 * Resolves a promise with a network timeout error
 * @returns {Promise<{error: {type: string, errors: {message: string}[]}}>}
 */
export function handleNetworkTimeoutError() {
  return Promise.resolve({
    error: {
      type: 'request_timeout',
      errors: [ {message: 'Your request has timed out.' } ]
    }
  });
}

/**
 * Resolves a promise with a no network error
 * @returns {Promise<{error: {type: string, errors: {message: string}[]}}>}
 */
export function handleNoNetworkError() {
  return Promise.resolve({
    error: {
      type: 'no_network',
      errors: [ {message: 'Please check your network connection.' } ]
    }
  });
}

/**
 * handleServiceCatch resolves a promise with error data from a failed service call
 * @param {object} error
 * @returns {Promise<{error: {messages, type}}>}
 */
export function handleServiceCatch(error) {
  return Promise.resolve({
    error: error.response.data
  });
}