import {getComponentWindow} from '../../createComponent';
import postRobot from '../../../post-robot-wrapper';

export function getLocalizedErrors(controllerId, country) {
  const controllerWindow = getComponentWindow(controllerId);

  // Send message to Controller Frame to call createSource
  return postRobot.send(controllerWindow, 'getLocalizationFiles', {
    country: country,
  }).then(response => {
    return response.data;
  });
  // TODO Webpack dynamic imports. This will make the code split and download and load dynamically.
  //return import(country + '.json'); //import('./localization/' + country);
}