import data from './data.json';

export function getTaxSpecificStrings(title, locale, stringsFromCountrySpec) {
  const formattedLocale = locale.replace('-','_');
  if(stringsFromCountrySpec?.[formattedLocale]) {
    return stringsFromCountrySpec[formattedLocale];
  }
  const specificStrings = data.taxIdSpecificStrings[title][formattedLocale];
  if (typeof specificStrings === 'undefined') {
    const stringsForCountry = data.taxIdSpecificStrings[title];
    const defaultLocale = Object.keys(stringsForCountry)[0];
    // eslint-disable-next-line no-console
    console.warn('Locale not supported. Using Default.', defaultLocale);
    return stringsForCountry[defaultLocale];
  }
  return data.taxIdSpecificStrings[title][formattedLocale];
}
export function getLocalizedStrings(locale) {
  const formattedLocale = locale.replace('-','_');
  return data.localizedStrings[formattedLocale];
}