import {
  createComponent,
  destroy,
  generateComponentId,
  getComponentWindow,
  onEventHandler,
  registerComponentWithController,
  unmount
} from '../../../client/createComponent';
import {sanitizeOptionsForType} from '../options';
import dataStore from '../../../client/dataStore';
import {sendEventData} from '../input-events';
import postRobot from '../../../post-robot-wrapper';
import {config} from '../config';
import {getLocaleFromLanguage} from '../localization/localized-messages';
import {complianceGetDetails} from '../../../client/complianceData';

let elements = {};
let componentData = {};
let controllerEmitter;
let controllerListener;
let handleGetComponentData;

const COMPONENT_TYPE = 'ideal';

/**
 *
 * @param controllerId
 * @param key
 * @param options
 * @returns {{unmount: unmount, controllerId: (*|null), options: (*|*), destroy: destroy, id: string, parentNode: null, type: string, mount: mountCreditCard, key: *, on: onEventHandler}}
 */
export function createIdeal(controllerId, key, options) {
  if(!options || !options.ideal || !options.ideal.sessionId) {
    throw Error('Please provide an options object containing ideal.sessionId');
  }
  const type = 'ideal';
  const id = generateComponentId(type);

  const idealComponent = {
    id: id,
    key: key,
    type: type,
    parentNode: null,
    controllerId: controllerId,
    mount: mountElement,
    destroy: destroyElement,
    on: onEventHandler,
    options: sanitizeOptionsForType(options, type) || {},
    unmount: unmountElement,
    window: Window
  };

  componentData = {
    componentType: COMPONENT_TYPE,
    componentId: id,
    controller: {
      id: controllerId,
      window: getComponentWindow(controllerId)
    },
    options: idealComponent.options,
    key: key
  };

  controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });
  controllerListener = postRobot.listener({window: componentData.controller.window, domain: config.domain});

  handleGetComponentData = controllerListener.on('getComponentData', getComponentData);
  return idealComponent;
}

function getComponentData() {
  return new Promise(resolve => resolve(componentData));
}

export function handleOptions(data) {

  const locale = data.instanceOptions.locale.length === 2 ? getLocaleFromLanguage(data.instanceOptions.locale) : data.instanceOptions.locale;
  const parentNodeId = componentData.parentNodeId;
  const componentId = componentData.componentId;

  componentData.instanceOptions = data.instanceOptions;
  componentData.options = data.options;
  componentData.recurring = data.recurring;
  componentData.sellingEntityId = data.sellingEntityId;
  componentData.country = data.country;
  componentData.mandateChecked = false;
  componentData.mandate = {
    text: getMandateValue(data.sellingEntityId, locale, data.country)
  };
  componentData.ibanComplete = false;

  if (componentData.recurring) {

    addContainerToDom(parentNodeId, componentId);

    const mandate = createMandate(componentData.sellingEntityId, locale, componentData.country);
    getContainerElement().appendChild(mandate);

  }

  emitComponentReady();
}



export function mountElement(parentNode) {
  if (this.parentNode !== null) {
    throw new Error(`Can not mount, only one ${this.type} can be mounted at a time.`);
  }

  if (typeof parentNode === 'string') {
    parentNode = document.getElementById(parentNode);
  }

  if (parentNode instanceof HTMLElement === false) {
    throw new Error('mount() requires a valid HTMLElement or id attribute.');
  }

  componentData.window = window;
  const data = dataStore.get(this.key);

  data.components[this.type] = { 'parent': parentNode, 'options': sanitizeOptionsForType(this.options, this.type), mandate: componentData.mandate };
  dataStore.set(this.key, data);

  this.parentNode = parentNode;

  componentData.parentNodeId = parentNode.id;

  return controllerEmitter.send('mountClientComponent', {
    componentId: componentData.componentId,
    componentType: componentData.componentType
  }).then((response) => {
    handleOptions(response.data);
  });

}
export function unmountElement() {
  if(elements.iban) {
    destroyIBAN(elements);
  }
  unmount.call(this);
}

function destroyElement() {
  if(elements.iban) {
    destroyIBAN(elements);
  }

  handleGetComponentData.cancel();
  return destroy.call(this);
}

function updateElementsState(elements, element, details) {
  elements[element] = Object.assign(elements[element], details);
}

export function handleFieldChange(details) {
  const elementType = 'iban';
  updateElementsState(elements, elementType, details);
  handleOnChangeEvent(elements, elementType, details, componentData);
}

export function handleOnChangeEvent(elements, elementType, details, componentData) {
  const {complete, empty, error} = details;
  const iDealData = {
    elementType: 'ideal',
    iban: {
      complete,
      empty,
      error
    }
  };
  updateIBANErrorMessages(elements.iban);
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'change', iDealData);
}

export function emitComponentReady() {
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'ready', getIdealData());
}

function getContainerElement() {
  return document.getElementById(componentData.componentId);
}

function getIdealData() {
  return {
    elementType: 'ideal'
  };
}

export function addContainerToDom(parentId, componentId) {
  document.getElementById(parentId).innerHTML += `<div id="${componentId}" class="DR-ideal-container"></div>`;
}

function createMandate(sellingEntityId, locale, country) {

  const mandateParent = document.createElement('div');
  mandateParent.className = 'DR-mandate DR-mandate-' + COMPONENT_TYPE;

  const mandateError = document.createElement('div');
  mandateError.id = determineMandateCheckboxErrorId(COMPONENT_TYPE);
  mandateError.className = 'DR-mandate-checkbox-error DR-error-message';
  mandateError.setAttribute('aria-live', 'assertive');

  const mandateRow = document.createElement('div');
  mandateRow.className = 'DR-mandate-row DR-mandate-row-' + COMPONENT_TYPE;
  const mandateText = document.createElement('div');
  mandateText.id = determineMandateTextId(COMPONENT_TYPE);

  const mandateCheckboxParent = document.createElement('div');
  const mandateCheckbox = document.createElement('input');
  mandateCheckbox.id = determineMandateCheckboxId(COMPONENT_TYPE);
  mandateCheckbox.setAttribute('name', determineMandateCheckboxId(COMPONENT_TYPE));
  mandateCheckbox.setAttribute('aria-errormessage', determineMandateCheckboxErrorId(COMPONENT_TYPE));
  mandateCheckbox.setAttribute('aria-labelledby', `${determineMandateTextId(COMPONENT_TYPE)}-label`);
  mandateCheckbox.type = 'checkbox';
  mandateCheckbox.addEventListener('change', handleMandateChange);

  mandateCheckboxParent.className = 'DR-mandate-checkbox DR-mandate-checkbox-' + COMPONENT_TYPE;
  mandateCheckboxParent.appendChild(mandateCheckbox);

  mandateRow.appendChild(mandateCheckboxParent);

  mandateText.className = 'DR-mandate-content DR-mandate-'+COMPONENT_TYPE;

  const mandateLabel = document.createElement('label');
  mandateLabel.setAttribute('for', mandateCheckbox.id);
  mandateLabel.id = `${determineMandateTextId(COMPONENT_TYPE)}-label`;
  mandateLabel.innerHTML = getMandateValue(sellingEntityId, locale, country);

  mandateText.appendChild(mandateLabel);

  mandateRow.appendChild(mandateText);

  mandateParent.appendChild(mandateRow);
  mandateParent.appendChild(mandateError);

  return mandateParent;
}


export function determineMandateTextId(paymentMethod) {
  return 'DR-mandate-text-'+paymentMethod;
}

export function determineMandateCheckboxErrorId(paymentMethod) {
  return 'DR-mandate-checkbox-error-'+paymentMethod;
}

export function determineMandateCheckboxId(paymentMethod) {
  return 'DR-mandate-checkbox-'+paymentMethod;
}

export function getMandateValue(sellingEntityId, locale) {
  const complianceInfo = complianceGetDetails(sellingEntityId, locale);
  const mandateText = complianceInfo.disclosure['idealRecurringAgreement'].localizedText;
  return mandateText;
}

export function handleMandateChange(event) {
  const controllerId = componentData.controller.id;
  componentData.mandateChecked = event.target.checked;
  if(componentData.mandateChecked) {
    elements = createAndMountIBAN(controllerId, componentData.key, componentData.options);
  } else {
    elements = destroyIBAN(elements);
  }
}

export function createAndMountIBAN(controllerId, key, options) {
  const ibanParent = document.createElement('div');
  ibanParent.id = 'DR-iban-ideal';


  getContainerElement().appendChild(ibanParent);
  const data = dataStore.get(key);

  const iban = {
    parentId: ibanParent.id,
    empty: true,
    complete: false
  };

  elements.iban = iban;
  iban.component = createComponent('iban', controllerId, key, options);
  data.components = Object.assign({}, data.components, { iban: iban.component.id });
  registerComponentWithController(controllerId, iban.component, iban.options);
  iban.component.on('change', handleFieldChange);

  iban.component.mount(ibanParent.id);
  const ibanError = createErrorMessageElement(iban.component.id);
  getContainerElement().appendChild(ibanError);
  return elements;
}

function destroyIBAN(elements) {
  getContainerElement().removeChild(getIBANParentElement());
  elements.iban.component.destroy();
  delete elements.iban;
  return elements;
}

function getIBANParentElement() {
  return document.getElementById('DR-iban-ideal');
}

function createErrorMessageElement(componentId) {
  const errorMessage = document.createElement('div');
  errorMessage.className = 'DR-error-message';
  errorMessage.id = `ideal-${componentId}-error`;
  errorMessage.setAttribute('aria-hidden', 'true');
  errorMessage.style.display = 'none';
  return errorMessage;
}

function updateIBANErrorMessages(ibanElement) {
  const {id} = ibanElement.component;
  const {error} = ibanElement;
  const errorEl = document.getElementById(`ideal-${id}-error`);
  if(error) {
    errorEl.innerHTML = error.message;
    errorEl.style.display = 'block';
  } else {
    errorEl.innerHTML = '';
    errorEl.style.display = 'none';
  }
}