import {createOverlayDiv, updateOverlay} from '../../createComponent';
import {
  attachStoreCreditToDropinCheckout, cloneDropinCheckout,
  createDropinCheckoutOrder, detachSourceToDropinCheckout,
  getDropinCheckout
} from '../checkout-sessions-api';
import {openWindow} from '../../../app/components/controller/controller-window-opener';
import {handleAfterSourceCreated} from './payment-api-call';
import {getButtonTextByLocale} from '../../dropin/button-text';
import {config} from '../../../app/components/config';

export function showSpinnerOverlay() {

  if (!document.getElementById('DR-payment-overlay')) {
    createOverlayDiv('DR-payment-overlay');
  }

  updateOverlay('DR-payment-overlay', '100%', 'rgba(0,0,0,0.3)');
}

export function removePaymentElementSpinner() {
  const overlay = document.getElementById('DR-payment-overlay');
  if (overlay !== null) {
    overlay.parentElement.removeChild(overlay);
  }
}

function addLoadingSpinnerToWindow(storage) {
  storage.window.document.head.innerHTML = `<link rel="stylesheet" href="${config.domain}${config.basePath}/css/DigitalRiver.css" type="text/css"/>`;

  const overlayStyle = 'position: fixed;left: 0;top: 0;width:100%;z-index: 10000;overflow:hidden;display:flex;justify-content:center;align-items:center; background-color: rgba(0,0,0,0.3); height: 100%';
  const overlay = document.createElement('div');
  overlay.setAttribute('style', overlayStyle);
  overlay.setAttribute('class', 'DR-spinner-overlay');
  storage.window.document.body.appendChild(overlay);
}

function displayDelayedPaymentInstructions(paymentSession) {
  return paymentSession?.state === 'pending_funds' && paymentSession.nextAction?.action === 'show_payment_instructions';
}

export function createOnSuccess(storage, configuration, handleNextAction, createElement) {
  return async function (successData) {
    /*if (storage.orderCreationData) {
      // TODO Delete order from session is not supported...
      //await deleteDropinCheckoutOrder(configuration.checkoutSessionId, storage.orderCreationData.order.id, storage.fingerprint, storage.apiKey);
    }*/

    /*if (storage.data) {
      // TODO delete source if one has already been attached.
      await detachSourceToDropinCheckout(configuration.checkoutSessionId, storage.data.source.id, storage.fingerprint, storage.apiKey)
    }*/
    storage.data = successData;
    storage.orderCreationData = undefined;

    showSpinnerOverlay();

    if (successData?.source?.state === 'pending_redirect') {
      storage.window = openWindow('');
      addLoadingSpinnerToWindow(storage);
    }

    return handleAfterSourceCreated(configuration, storage, storage.apiKey).then(async (data) => {
      storage.orderCreationData = data;

      const order = data.order;

      if (data.status === 'RETRY') {
        removePaymentElementSpinner();
        const message = storage.translations.error.createOrderFailed;
        setErrorMessage(message);
        storage.dropinInstance.refresh();
        return;
      } else if (data.status === 'STOP' || data.status === 'BAD_ORDER') {

        const clonedDropinCheckout = await cloneDropinCheckout(configuration.checkoutSessionId, storage.fingerprint, storage.apiKey).then(data => data.json());

        configuration.checkoutSessionId = clonedDropinCheckout.id;
        storage.checkoutSessionId = clonedDropinCheckout.id;
        storage.fingerprint = undefined;

        storage.checkoutData = await getDropinCheckout(configuration.checkoutSessionId, storage.apiKey, storage.fingerprint).then(data => data.json());
        storage.fingerprint = storage.checkoutData.fingerprint;

        storage.notify(storage.checkoutData);

        removePaymentElementSpinner();
        const message = storage.translations.error.tryAnotherPaymentMethod;
        setErrorMessage(message);
        return;
      }

      const paymentSession = order?.payment?.session;
      if (isRedirectAction(order?.payment?.session?.nextAction?.action)) {
        // NOTE: Need to do this. We also could try open before API calls IF redirect payment method
        const selectedPaymentMethodType = data.order.payment.sources[0].type;
        storage.windowOpenerURL = paymentSession.nextAction.data.redirectUrl;
        storage.selectedPaymentMethodType = selectedPaymentMethodType;
      } else if (order && typeof order.errors !== 'undefined' && order.errors[0].code === 'additional_payment_action_required') {

        // Retrieve checkout
        const checkout = await getDropinCheckout(configuration.checkoutSessionId, storage.apiKey, storage.fingerprint).then(data => data.json());

        removePaymentElementSpinner();

        if (checkout.payment?.session?.nextAction?.action === 'sca_required') {
          const response = await handleNextAction(checkout.payment.session.nextAction);

          if (response.status === 'complete') {
            showSpinnerOverlay();
            const order = await createDropinCheckoutOrder(configuration.checkoutSessionId, storage.fingerprint, storage.apiKey).then(r => r.json());
            removePaymentElementSpinner();
            storage.complete({
              order: order
            });
          } else {
            const message = storage.translations.error.createOrderFailed;
            setErrorMessage(message);
          }

        }

      } else if (displayDelayedPaymentInstructions(paymentSession)) {


        if (document.getElementById('DR-delayed-payment-instructions') !== null) {
          const options = {
            sourceId: paymentSession.nextAction.data.sourceId,
            sourceClientSecret: paymentSession.nextAction.data.sourceClientSecret
          };
          const delayedPaymentInstructions = createElement('delayedpaymentinstructions', options);
          delayedPaymentInstructions.mount('DR-delayed-payment-instructions');
        }

        storage.complete({
          order: data.order,
          delayedPaymentInstructions: {
            sourceId: paymentSession.nextAction.data.sourceId,
            sourceClientSecret: paymentSession.nextAction.data.sourceClientSecret
          }
        });

      } else if (order && typeof order.errors !== 'undefined') {
        removePaymentElementSpinner();
        const message = storage.translations.error.unknownError;
        setErrorMessage(message);
      } else {
        storage.complete({
          order: data.order
        });
      }

      storage.notify(storage.checkoutData);

    });
  };
}

function isRedirectAction(action) {
  return action === 'redirect';
}

export function createOnReady(storage, callApiAndMount) {
  return function () {
    storage.subscribers.push(callApiAndMount);
  };
}

export function createOnCancel(storage) {
  return function () {
    removePaymentElementSpinner();
    setPendingRedirectMessage(storage, storage.controller);
  };
}

export function setPendingRedirectMessage(storage, controller) {
  if (storage.orderCreationData && isRedirectAction(storage.orderCreationData.order?.payment?.session?.nextAction?.action)) {
    setErrorMessage(storage.translations.error.orderNotComplete);
    const submitButton = document.getElementById(`submit-${storage.selectedPaymentMethod.getType()}-${controller.id}`);
    if (submitButton !== null) {
      submitButton.innerText = getButtonTextByLocale(storage.locale, 'completeOrder');
      submitButton.onclick = function () {
        openWindow(storage.orderCreationData.order.payment.session.nextAction.data.redirectUrl);
      };
    }
  }
}

export function createOnError(storage) {
  return function (data) {
    if (data.errors && data.errors.length > 0 && data.errors[0].code === 'source_creation_failed') {
      const localizedDisplayName = storage.selectedPaymentMethod.getLocalizedName();
      const message = storage.translations.error.dropinError.replace('{{paymentMethod}}', localizedDisplayName);
      setErrorMessage(message);
    } else {
      const message = storage.translations.error.createOrderFailed;
      setErrorMessage(message);
    }
  };
}

export function createOnUpdateRequired(storeCredits, configuration, storage) {
  return async (data) => {

    showSpinnerOverlay();

    const pm = data.storedPaymentMethods.find(spm => spm.id === data.selectedPaymentMethod.id);
    pm.added = data.selectedPaymentMethod.action === 'add';

    const selectedStoreCredit = data.selectedPaymentMethod;
    const selectedUpstreamId = selectedStoreCredit.id;

    const selectedCredit = storeCredits.find(obj => obj.id === selectedUpstreamId);

    const action = selectedStoreCredit.action;

    if (action === 'add') {
      const storeCreditResponse = await attachStoreCreditToDropinCheckout(configuration.checkoutSessionId, selectedUpstreamId, storage.fingerprint, storage.apiKey);

      const responseJson = await storeCreditResponse.json();
      if (storeCreditResponse.status < 300 && !responseJson.error) {
        selectedCredit.sourceId = responseJson.id;
        data.updateWith(data.storedPaymentMethods);
      } else {
        // TODO Get official error
        const message = storage.translations.error.unknownError;
        setErrorMessage(message);
      }

    } else {

      const selectedSourceId = selectedCredit.sourceId;

      const response = await detachSourceToDropinCheckout(configuration.checkoutSessionId, selectedSourceId, storage.fingerprint, storage.apiKey);
      if (response.status < 300) {
        delete selectedCredit.sourceId;
        data.updateWith(data.storedPaymentMethods);
      } else {
        // TODO Get official error
        const message = storage.translations.error.unknownError;
        setErrorMessage(message);
      }

    }

    removePaymentElementSpinner();
  };
}

export function setErrorMessage(errorMessage) {
  const selectedPaymentMethod = document.querySelector('.DR-show');
  const paymentMethod = selectedPaymentMethod.getAttribute('data-type');
  const id = 'DR-order-'+paymentMethod;

  const existingErrorMessageDiv = document.getElementById(id);
  if (existingErrorMessageDiv === null) {
    const errorMessageDiv = document.createElement('div');
    errorMessageDiv.id = id;
    errorMessageDiv.className = 'DR-error-message';
    errorMessageDiv.innerText = errorMessage;
    errorMessageDiv.style.display = 'block';

    const continueButton = document.querySelector('.DR-show button');
    if (continueButton !== null) {
      selectedPaymentMethod.insertBefore(errorMessageDiv, continueButton);
    } else {
      selectedPaymentMethod.prepend(errorMessageDiv);
    }

  } else {
    existingErrorMessageDiv.innerText = errorMessage;
    existingErrorMessageDiv.style.display = 'block';
  }
}