import { h, render, Component }  from 'preact';
import {createWhenExists} from '../utils';
import {paymentMethodDetails} from './element-details';

const walletElements = [
  'googlePay',
  'applePay',
  'payPal'
];

function buildUpdateWithResponse(result) {

  if (!result.status) {
    return {
      status: 'failure',
    };
  }

  return {
    status: 'success',
    total: {amount: parseFloat(result?.data?.totalAmount)}
  };

}

class WalletElement extends Component {

  constructor(props) {
    super(props);

    this.state = {
      //paymentMethods: props.paymentMethods
    };

  }

  addElementToStorage(element) {
    this.props.addElementToStorage(element);
  }

  render() {

    const {createElement, getMountedElements, paymentSessionId, sessionInformation, processSourceData, attachShippingAddressToCheckoutSession, attachShippingChoiceToCheckoutSession, checkoutData, shippingMethods, error, locale, controllerId} = this.props;

    const paymentMethods = this.props.paymentMethods.filter( pm => walletElements.includes(pm.type));

    const elementsCreated = getMountedElements();

    return (
      <div className={'DR-wallet-container'}>

        {paymentMethods?.length > 0 && <div className={'DR-wallet-header'}>
          {this.props.translations.sectionHeading.expressCheckout}
        </div>}

        <div className={'DR-wallet-items'}>
          {paymentMethods.map( pm => {

            const paymentMethod = paymentMethodDetails().find(paymentMethod => paymentMethod.type === pm.type);


            const componentNotMounted = typeof elementsCreated.find(e => e?.componentInstance.type.toLowerCase() === paymentMethod.code.toLowerCase()) === 'undefined';

            if (componentNotMounted) {

              this.addElementToStorage({
                componentInstance: {
                  type: paymentMethod.code
                }
              });

              createWhenExists('parent-' + paymentMethod.type, (parent) => {

                const elementCreated = paymentMethod.component({
                  paymentSessionId: paymentSessionId,
                  parent: parent,
                  checkoutData: checkoutData,
                  shippingMethods: shippingMethods,
                  paymentMethodResponse: {
                    sessionInformation: sessionInformation,
                    paymentMethods: paymentMethods
                  },
                  availablePaymentMethod: pm,
                  createElement: createElement,
                  paymentMethod: paymentMethod,
                  locale: locale,
                  controllerId: controllerId,
                  addElementToStorage: this.addElementToStorage,
                });

                if (elementCreated !== null && typeof elementCreated !== 'undefined' && elementCreated.componentInstance) { // In case the element fails or canMakePayment returns false...
                  this.addElementToStorage(elementCreated);

                  elementCreated.componentInstance.on('shippingaddresschange', async function (details) {

                    const result = await attachShippingAddressToCheckoutSession({
                      shipTo: {
                        address: {
                          line1: '.', // TODO *** Required by API. Will be removed in the future. ***
                          city: details.shippingAddress.address.city,
                          state: details.shippingAddress.address.state,
                          country: details.shippingAddress.address.country,
                          postalCode: details.shippingAddress.address.postalCode
                        }
                      }
                    });

                    if (details.updateWith) {
                      details.updateWith(buildUpdateWithResponse(result));
                    }
                  });
                  elementCreated.componentInstance.on('shippingoptionchange', async function (details) {
                    const result = await attachShippingChoiceToCheckoutSession(details.shippingOption.id);


                    if (details.updateWith) {
                      details.updateWith(buildUpdateWithResponse(result));
                    }
                  });
                  elementCreated.componentInstance.on('source', async function (data) {
                    const result = await processSourceData(data);

                    if (data.complete) {
                      const status = result.status ? 'success' : 'failure';
                      data.complete(status);
                    }

                  });
                }

              });

            }

            return <div id={'parent-'+paymentMethod.type}  className={'DR-wallet-item'} >

            </div>;


          })}

        </div>


        {error?.length > 0 && <div className={'DR-error-message'}>
          {error}
        </div>}

      </div>
    );
  }
}



export function appendWallet(id, options) {
  return render(<WalletElement {...options} />, id);
}