import { h, render, Component }  from 'preact';
import {getComponentsBasePath} from '../../createComponent';

function determineTotal(shouldAddTax, total, tax) {
  if (shouldAddTax) {
    return total + tax;
  } else {
    return total;
  }
}

class OrderSummaryElement extends Component {

  render() {


    const data = this.props.data;

    if (!data) {
      return <div className={'DR-order-summary-container'}>
      </div>;
    }

    const locale = this.props.data?.locale?.replace('_', '-');

    const shouldAddTax = data?.taxInclusive === true;
    return (
      <div className={'DR-order-summary-container'}>
        <div className={'DR-order-summary-heading'}>{this.props.translations.sectionHeading.orderSummary}</div>
        {data?.items?.map( item => {
          return <div className={'DR-order-summary-item-row'}>
            <div className={'DR-order-summary-item-details'}>
              <div>
                {item?.sku?.image?.length > 0 && <img className={'DR-order-summary-sku-image'} src={item.sku.image} />}
              </div>
              <div className={'DR-order-summary-sku-name'}>
                <div>{item.sku.name}</div>
                <div className={'DR-order-summary-sku-quantity'}>
                  {this.props.translations.orderSummaryTable.qtyColon} {item.quantity}
                </div>
              </div>

            </div>
            <div>
              <div className={'DR-order-summary-sku-name'}>
                {new Intl.NumberFormat(locale, { style: 'currency', currency: data.currency }).format(determineTotal(shouldAddTax, item.amount, item.tax.amount))}
              </div>
              
            </div>
            
          </div>;
        })}

        <hr />

        <div className={'DR-order-summary-sku-row'}><div>{this.props.translations.orderSummaryTable.subtotal}:</div> <div>{new Intl.NumberFormat(locale, { style: 'currency', currency: data.currency }).format(data.subtotal)}</div></div>
        {data?.options?.shippingMethods?.length > 0 &&  <div className={'DR-order-summary-sku-row DR-order-summary-shipping-cost'}>{this.props.translations.orderSummaryTable.shippingCost}: <div>{new Intl.NumberFormat(locale, { style: 'currency', currency: data.currency }).format(data.totalShipping)}</div></div>}
        {data.taxInclusive === false && <div className={'DR-order-summary-sku-row DR-order-summary-total-tax'}>{this.props.translations.orderSummaryTable.tax} <div>{new Intl.NumberFormat(locale, { style: 'currency', currency: data.currency }).format(data.totalTax)}</div></div>}

        <hr />

        <div className={'DR-order-summary-sku-row DR-order-summary-total'}>{this.props.translations.orderSummaryTable.total} <div>{new Intl.NumberFormat(locale, { style: 'currency', currency: data.currency }).format(data.totalAmount)}</div></div>
        {data.taxInclusive === true && <div className={'DR-order-summary-vat'}>{this.props.translations.orderSummaryTable.includesVatOf} {new Intl.NumberFormat(locale, { style: 'currency', currency: data.currency }).format(data.totalTax)}</div>}

      </div>
    );
  }
}

export function appendOrderSummary(id, options) {
  return render(<OrderSummaryElement {...options} />, id);
}