function transformStoreCredits(storage) {

  if (typeof storage.checkoutData?.options?.storeCredits === 'undefined') {
    return [];
  }

  return storage.checkoutData?.options?.storeCredits?.map(sc => {
    return {
      ...sc,
      type: 'storeCredit',
      id: sc.upstreamId,
      displayName: sc.name
    };
  });
}

function transformStoredCreditCards(storage) {

  if (typeof storage.checkoutData?.options?.sources === 'undefined') {
    return [];
  }

  return storage.checkoutData?.options?.sources?.filter(s => s.type === 'creditCard' && s.reusable === true).map(s => {
    return {
      lastFour: s[s.type].lastFourDigits,
      type: s.type,
      brand: s[s.type].brand,
      id: s.id,
    };
  });
}

export function getStoredPaymentMethods(storage) {

  if (typeof storage.checkoutData?.options?.storeCredits === 'undefined' && typeof storage.checkoutData?.options?.sources === 'undefined') {
    return undefined;
  }

  if (storage.checkoutData?.options?.storeCredits?.length === 0 && storage.checkoutData?.options?.sources?.length === 0) {
    return undefined;
  }

  const storeCredits = transformStoreCredits(storage);
  const storedCreditCards = transformStoredCreditCards(storage);

  const storedPaymentMethods = [
    ...storeCredits,
    ...storedCreditCards
  ];

  if (storedPaymentMethods.length === 0) {
    return undefined;
  } else {
    return storedPaymentMethods;
  }

}