import {invoiceAttributeRequest} from '../../payment-service-request';
import {config} from '../config';
import {handleNetworkTimeoutError, handleNoNetworkError, handleServiceCatch, handleSingleError, handleUnauthorizedError} from '../error-utils';

export function runGetInvoiceAttributeAndHandleResponse(apiKey, data) {
  return invoiceAttributeRequest(data, apiKey, `${config.apiUrl}/invoice-attributes`).then((response) => {
    return response.data;
  }).catch((error) => {
    return chooseInvoiceAttributeCatchMessage(error);
  });
}

/**
 * chooseCreateSourceCatchMessage returns appropriate error when create source fails
 * @param {*} error
 * @returns {*}
 */
export function chooseInvoiceAttributeCatchMessage(error) {

  if (typeof error === 'string') {
    return handleSingleError(error);
  } else if (error.response) {
    if (error.response.status === 401) {
      return handleUnauthorizedError();
    } else {
      return handleServiceCatch(error);
    }
  } else {
    if (error.message && error.message.includes('timeout')) {
      return handleNetworkTimeoutError();
    } else {
      return handleNoNetworkError();
    }
  }

}