export function addTerms(bodyParent, availablePaymentMethod, paymentMethod, locale) {

  if (availablePaymentMethod.paymentDescription) {
    const paymentDescription = document.createElement('div');
    paymentDescription.className = 'DR-payment-description';
    paymentDescription.innerText = availablePaymentMethod.paymentDescription;
    bodyParent.appendChild(paymentDescription);
  }

  const titleContainer = document.createElement('div');
  titleContainer.className = 'DR-disclosure-title-container';
  const title = document.createElement('div');
  title.className = 'DR-disclosure-title';
  title.innerHTML = 'Civilité';

  if (paymentMethod.displaySalutation) {

    const options = ['', 'Mlle', 'Mme', 'M'];
    const selectList = document.createElement('select');
    selectList.id = 'DR-' + paymentMethod.type + '-select-title';
    selectList.className = 'DR-disclosure-select';
    selectList.setAttribute('aria-invalid', 'false');
    options.forEach(o => {
      const option = document.createElement('option');
      option.value = o;
      option.text = o;
      selectList.appendChild(option);
    });

    titleContainer.appendChild(title);
    titleContainer.appendChild(selectList);
  }

  bodyParent.appendChild(titleContainer);

  const selectedTermContainer = document.createElement('div');
  selectedTermContainer.className = 'DR-selected-terms DR-standard-padding DR-selected-terms-'+availablePaymentMethod.type;

  const termsContainer = document.createElement('div');
  termsContainer.setAttribute('role', 'radiogroup');

  availablePaymentMethod.terms.forEach( termFromAPI => {

    const name = paymentMethod.type+'-choice-terms';
    const termLabelId = name + '-' + termFromAPI.id;

    const termRowContainer = document.createElement('div');
    termRowContainer.className = 'DR-dynamic-terms';

    const termRadioButton = document.createElement('input');
    termRadioButton.type = 'radio';
    termRadioButton.id = termLabelId;
    termRadioButton.name = name;
    termRadioButton.className = 'DR-dynamic-terms-radio';
    termRadioButton.onclick = function () {
      selectedTermContainer.innerHTML = findDisclosureByLocale(termFromAPI.disclosures, locale).description;
    };
    termRadioButton.value = termFromAPI.id;


    const description = termFromAPI.localizedText?.disclosure_description || termFromAPI.localizedText?.description;

    termRowContainer.appendChild(termRadioButton);

    if (termFromAPI.localizedText?.icon) {
      const termIconContainer = document.createElement('div');
      termIconContainer.className = 'DR-dynamic-terms-container';
      const termIcon = document.createElement('img');
      termIcon.className = 'DR-dynamic-terms-image';
      termIcon.src = termFromAPI.localizedText.icon;
      termIcon.alt = description;
      termIconContainer.appendChild(termIcon);
      termRowContainer.appendChild(termIconContainer);
    }

    // Note: Description can be undefined but is going to get fixed.
    const termText = document.createElement('label');
    termText.innerText = description;
    termText.setAttribute('for', termLabelId);
    termRowContainer.appendChild(termText);

    termsContainer.appendChild(termRowContainer);
  });

  bodyParent.appendChild(termsContainer);
  bodyParent.appendChild(selectedTermContainer);

  const errorContainer = document.createElement('div');
  errorContainer.id = determineDynamicTermsCheckboxErrorId(paymentMethod);
  errorContainer.className = 'DR-terms-error DR-error-message';
  bodyParent.appendChild(errorContainer);
}

export function determineDynamicTermsCheckboxErrorId(paymentMethod) {
  return 'DR-terms-error-'+paymentMethod.type;
}

function findDisclosureByLocale(disclosures, locale) {

  const localeToMatchAPI = locale.replace('-', '_');
  const disclosureByLocale = disclosures.find( d => d.locale === localeToMatchAPI );

  if (typeof disclosureByLocale !== 'undefined') {
    return disclosureByLocale;
  }

  return disclosures[0];
}

export function getSelectTitleErrorMessage() {
  return 'Veuillez effectuer une sélection afin de continuer';
}
