import {dataChangedThatRequiresRecheck, validateClientDomElement, waitForRequiredData} from '../utils';
import {handleRedirectComplete, handleRedirectResponse} from './payment-api-call';
import {createOnCancel, createOnError, createOnReady, createOnSuccess, createOnUpdateRequired} from './payment-events';
import {getStoredPaymentMethods} from './checkout-session-payment-adapter';

function determineConsents(checkoutSession) {
  if (checkoutSession?.options?.consents && checkoutSession.options.consents.companyName) {
    const consents = checkoutSession.options.consents;
    const consentOptions = {};
    consentOptions.companyName = consents.companyName;

    if (consents.eula?.url) {
      consentOptions.eula = {...consents.eula};
    }

    if (consents.termsOfService?.url) {
      consentOptions.termsOfUse = {...consents.termsOfService};
    }

    return consentOptions;
  } else {
    return undefined;
  }
}

export function payment(key, configuration, storage, createDropin, handleNextAction, createElement) {

  let nodeToKeep;
  const mountedVersion = {
    instance: undefined,
    data: {}
  };

  const callApiAndMount = async function () {

    console.log('!!!!!!!!PAYMENT MOUNT', mountedVersion.instance, storage.checkoutData);

    const dropinOnCancel = createOnCancel(storage);

    const checkoutSession = storage.checkoutData;

    const sessionId = storage.checkoutData.payment.session.id;
    const customerType = storage.checkoutData.customerType;
    const sellingEntity = storage.checkoutData?.sellingEntity?.id;
    const shippingChoice = storage.checkoutData?.shippingChoice?.description;

    const storedPaymentMethods = getStoredPaymentMethods(storage);

    if (!mountedVersion.instance) {

      handleRedirectComplete(storage.controller, storage, dropinOnCancel);

      handleRedirectResponse(storage.controller, storage);

      const isLoggedIn = storage.checkoutData?.customerId?.length > 0;

      const dropinInstance = createDropin({
        sessionId: sessionId,
        onError: createOnError(storage),
        onCancel: dropinOnCancel,
        onReady: createOnReady(storage, callApiAndMount),
        onSuccess: createOnSuccess(storage, configuration, handleNextAction, createElement),
        options: {
          showComplianceSection: true,
          showTermsOfSaleDisclosure: true,
          showSavePaymentAgreement: isLoggedIn,
          redirect: {
            disableAutomaticRedirects: true
          },
          storedPaymentMethods: storedPaymentMethods,
          consents: determineConsents(checkoutSession)
        },
        onUpdateRequired: createOnUpdateRequired(storedPaymentMethods, configuration, storage)
      });

      dropinInstance.mount(nodeToKeep);

      mountedVersion.instance = dropinInstance;
      mountedVersion.data.paymentSessionId = sessionId;
      mountedVersion.data.customerType = customerType;
      mountedVersion.data.sellingEntity = sellingEntity;
      mountedVersion.data.shippingChoice = shippingChoice;

    } else if (typeof mountedVersion.instance !== 'undefined' &&
      (dataChangedThatRequiresRecheck(storage, mountedVersion.data.customerType, mountedVersion.data.paymentSessionId, mountedVersion.data.sellingEntity) ||
        shippingChoice !==  mountedVersion.data.shippingChoice)) {
      mountedVersion.data.paymentSessionId = sessionId;
      mountedVersion.data.customerType = customerType;
      mountedVersion.data.sellingEntity = sellingEntity;
      mountedVersion.data.shippingChoice = shippingChoice;

      mountedVersion.instance.refresh(nodeToKeep, sessionId);
    }

  };

  return {
    mount: (node) => {

      nodeToKeep = validateClientDomElement(node);

      storage.subscribers.push(callApiAndMount);

      waitForRequiredData(storage).then( () => {
        callApiAndMount(storage.checkoutData);
      });

    },
  };
}


