import {getTermsCheckboxDOMElement, isTermsDisplayed} from './terms';
import {getMandateCheckboxDOMElement, isMandateDisplayed} from './mandate-content';
import {createSourceData} from '../create-dropin';

export function addSendUpdateEventsForGooglePay(configuration, paymentMethod, controller, availablePaymentMethod, paymentMethodResponse, locale, component) {
  if (isTermsDisplayed(paymentMethod)) {
    getTermsCheckboxDOMElement(paymentMethod).addEventListener('change', changeCallback(configuration, paymentMethod, controller, availablePaymentMethod, paymentMethodResponse, locale, component));
  }

  if (isMandateDisplayed(paymentMethod)) {
    getMandateCheckboxDOMElement(paymentMethod).addEventListener('change', changeCallback(configuration, paymentMethod, controller, availablePaymentMethod, paymentMethodResponse, locale, component));
  }
}

function changeCallback(configuration, paymentMethod, controller, availablePaymentMethod, paymentMethodResponse, locale, component) {
  return function () {

    const source = createSourceData(configuration, paymentMethod, controller.id, availablePaymentMethod, paymentMethodResponse.sessionInformation, locale);

    const agreementData = typeof source.mandate === 'undefined' ? {useMandate: false} : {
      useMandate: true,
      mandate: source.mandate,
      futureUse: source.futureUse
    };

    component.update({
      agreementData: agreementData
    });
  };
}