const TIMEOUT_DURATION = 6000;

export function validateClientDomElement(node) {
  if (typeof node === 'string') {
    node = document.getElementById(node);
  }

  if (!(node instanceof HTMLElement)) {
    throw new Error('mount() requires a valid HTMLElement or id attribute.');
  }

  return node;
}

export function waitForRequiredData(storage) {
  return new Promise(resolve => {
    const interval = setInterval(() => {
      if (typeof storage.fingerprint !== 'undefined' && typeof storage.translations !== 'undefined') {
        clearInterval(interval);
        resolve();
      }
    }, 100);

    setTimeout(() => {
      clearInterval(interval);
    }, TIMEOUT_DURATION);

  });
}

export function waitForRequiredDataOnAddress(storage) {
  return new Promise(resolve => {
    const interval = setInterval(() => {
      if (typeof storage.fingerprint !== 'undefined' && typeof storage.translations !== 'undefined' && (typeof storage.jsonSchematranslations !== 'undefined')) {
        clearInterval(interval);
        resolve();
      }
    }, 100);

    setTimeout(() => {
      clearInterval(interval);
    }, TIMEOUT_DURATION);

  });
}

export function createWhenExists(id, fn) {

  const i = setInterval(function () {
    if (document.getElementById(id) !== null) {
      clearInterval(i);
      try {
        fn(document.getElementById(id));
        // eslint-disable-next-line no-empty
      } catch (e) {
      }
    }
  }, 100);

  setTimeout(function () {
    clearInterval(i);
  }, 4000);

}

export function dataChangedThatRequiresRecheck(storage, storedCustomerType, storedPaymentSessionId, storedSellingEntity) {
  const customerType = storage.checkoutData.customerType;
  const paymentSessionId = storage.checkoutData?.payment?.session.id;
  const sellingEntity = storage.checkoutData?.sellingEntity?.id;

  return storedCustomerType !== customerType ||
    storedPaymentSessionId !== paymentSessionId ||
    storedSellingEntity !== sellingEntity;
}
