import {getComponentWindow} from '../../createComponent';
import postRobot from '../../../post-robot-wrapper';

export function getLocalizedJsonSchema(controllerId, locale) {
  const controllerWindow = getComponentWindow(controllerId);

  return postRobot.send(controllerWindow, 'getLocalizationJsonSchema', {
    locale: locale,
  }).then(response => {
    return response.data;
  });
}