import {
  DEFAULT_LOCALE,
  getCountryFromLocale,
  getLocaleFromLanguage
} from '../../app/components/localization/localized-messages';
import data from '../../json/compliance.json';

/**
 * Determines whether a country requires a specific legal link type
 * @param {string} country - 2 char ISO code
 * @param {string} linkType
 * @returns {boolean | undefined}
 */
export function getRequiredLegalLinks(country, linkType) {
  return countryLegalRequirements[country] ? countryLegalRequirements[country][linkType] : defaultCountryLegalRequirements[linkType];
}

/**
 * Gets display name for entity code
 * @param {string} entityId
 * @returns {string} business entity name
 */
export function getBusinessEntityNameFromCode(entityId) {
  let entityName;
  //TODO maybe use find here instead
  data.entityCode.forEach( function (entity) {
    if(typeof entityId === 'string' && entityId.toUpperCase() === entity.code) {
      entityName = entity.name;
    }
  });
  return entityName;
}

/**
 *
 * @param {Object} complianceOptions
 * @param {string} complianceOptions.country
 * @param {string} complianceOptions.language
 * @returns {string} language locale format 'en_US'
 */
function getLanguageLocaleFromComplianceOptions(complianceOptions) {
  const {country, language} = complianceOptions;
  const languageLocaleFromOptions = `${language}_${country.toUpperCase()}`;
  return data.locale.includes(languageLocaleFromOptions) ? languageLocaleFromOptions : getLocaleFromLanguage(language).replace('-','_');
}

/**
 *
 * @param {string} clientLanguage - format 'en' or 'en_US' (underscore)
 * @returns {string} language locale format 'en_US'
 */
function getLanguageLocaleFromClientLanguage(clientLanguage) {
  const formattedClientLanguage = clientLanguage.replace('-', '_');
  const mappedLocaleFromFormattedClientLanguage = getLocaleFromLanguage(formattedClientLanguage);
  let language;
  if(data.locale.includes(formattedClientLanguage)) {
    // we have translations for this exact input
    language = formattedClientLanguage;
  } else if(mappedLocaleFromFormattedClientLanguage !== DEFAULT_LOCALE.replace('-', '_')) {
    // getting locale from language isn't just defaulting - needed for inputs like es_419
    language =  mappedLocaleFromFormattedClientLanguage;
  } else {
    // trying one last time, this will get default locale if all others have failed
    language =  getLocaleFromLanguage(formattedClientLanguage.substring(0,2));
  }
  return language.replace('-', '_');
}

/**
 * Handles arguments for getComplianceDetails
 * @param entityInfo {object|string} - If client passes an object, it will contain country, language, and businessEntityCode. Otherwise it will be a string containing businessEntityCode
 * @param clientLanguage {string}
 * @param jpCommerceLawPageUrl {string | undefined}
 * @returns {{linkLocale: string, country: string, entityName: string, encodedEntityName: string, locale: string, entity: (string|Object)}}
 */
export function handleComplianceOptions(entityInfo, clientLanguage, clientJpCommerceLawPageUrl) {
  let errors = [];
  const isEntityInfoAnObject = typeof entityInfo === 'object';

  try {
    const entity = !isEntityInfoAnObject ? entityInfo : entityInfo.businessEntityCode ? entityInfo.businessEntityCode.toUpperCase() : undefined;
    const entityName = getBusinessEntityNameFromCode(entity);
    const encodedEntityName = encodeURIComponent(entityName);

    if (!entityName) {
      errors.push('businessEntityCode is missing or unsupported');
    }

    // country will be used to determine which legal items are used for each country
    const country = !isEntityInfoAnObject ? getCountryFromLocale(clientLanguage) : entityInfo.country ? entityInfo.country.toUpperCase() : undefined;
    if ((!country || country === '') && isEntityInfoAnObject) {
      // country is only an argument when entityInfo is an object
      const countryError = 'country is missing or unsupported';
      errors.push(countryError);
    }

    // languageLocale will be used to determine the translation locale
    if(isEntityInfoAnObject && !entityInfo.language) {
      // language is only an argument when entityInfo is an object
      errors.push('language is missing or unsupported');
    }

    const languageLocale = isEntityInfoAnObject ? getLanguageLocaleFromComplianceOptions(entityInfo) : getLanguageLocaleFromClientLanguage(clientLanguage);

    const jpCommerceLawPageUrl = isEntityInfoAnObject ? entityInfo.jpCommerceLawPageUrl : clientJpCommerceLawPageUrl;


    if(errors.length > 0) {
      throw new Error(errors.join(', '));
    }

    const complianceOptions =  {
      entity,
      entityName,
      encodedEntityName,
      country,
      languageLocale,  //TODO we probably do not need both language locale and link locale here. they are the same. we should just use languageLocale
      linkLocale: languageLocale
    };

    if(jpCommerceLawPageUrl) {
      complianceOptions.jpCommerceLawPageUrl = jpCommerceLawPageUrl;
    }

    return complianceOptions;
  } catch(err) {
    throw new Error(errors.join(', '));
  }
}

/**
 * Returns array of legal link types
 * @returns {string[]}
 */
export function getLegalLinkTypes() {
  return LEGAL_LINKS;
}

