import { h, render, Component, Fragment }  from 'preact';
import {getPlaceholderTranslationByKey} from './address-translations-utils';
import {parseFullName} from 'parse-full-name';
import {defaultError} from "./address-mount";

function InputField(props) {

  const error = props.errors[props.addressType][props.propertyKey];

  return <div>
    <input
      id={generateID(props.propertyKey, props.addressType)}
      name={generateID(props.propertyKey, props.addressType)}
      type={'text'}
      className={isHighlightError(error) ? 'DR-validation-error-highlight DR-address-item' : 'DR-address-item'}
      maxLength={props.property.maxLength}
      value={props.value || ''}
      onInput={(event) => props.handleUpdateAddress(event, props.propertyKey, props.addressType, props.required)}
      onBlur={(event) => props.handleUpdateAddress(event, props.propertyKey, props.addressType, props.required, props.property.pattern)}
      placeholder={!props.required ? props.translations.placeholder.optional : ''}
    />
    {displayValidationMessage(error) && <div className={'DR-validation-error-message'}>{error.message}</div>}
  </div>;
}

function isHighlightError(error) {
  return error?.code?.length > 0 && error?.message?.length === 0;
}

function displayValidationMessage(error) {
  return typeof error !== 'undefined' && error?.message?.length > 0;
}

function Select(props) {

  const error = props.errors[props.addressType][props.propertyKey];
  const billingCountryPreSelected = (props.shoppingCountry && props.addressType === 'billingAddress') ? props.shoppingCountry : '';
  return <div>
    {props.property.enum.length === 1 && 
    <select
      className={'DR-address-item'}
      id={generateID(props.propertyKey, props.addressType)}
      name={generateID(props.propertyKey, props.addressType)}>
      {props.property.enum.map( e => {
        return <option value={e.countryCode} selected={true}>{e.countryName}</option>;
      })}
    </select>
    }
    {props.property.enum.length !== 1 && 
    <select
      className={isHighlightError(error) ? 'DR-validation-error-highlight DR-address-item' : 'DR-address-item'}
      id={generateID(props.propertyKey, props.addressType)}
      name={generateID(props.propertyKey, props.addressType)}
      onChange={(event) => props.handleUpdateAddress(event, props.propertyKey, props.addressType, props.required)} value={props.value || billingCountryPreSelected || ''}>
      <option value={''}>{props.translations.misc.select}</option>
      {props.property.enum.map( e => {
        return <option value={e.countryCode} selected={billingCountryPreSelected === e.countryCode}>{e.countryName}</option>;
      })}
    </select>
    }
  </div>;
}

function generateID(key, type) {
  return 'DR-'+type+'-'+key;
}

function AddressRowTitle(props) {
  return <div>
    <label htmlFor={generateID(props.propertyKey, props.addressType)}>{props.translatedTitle} {props.required && <span>&#42;</span>}</label>
  </div>;
}

function AddressTypeSelection(props) {
  return <div className={'DR-shipping-address-selection'}>
    <div>
      <input id="DR-shipping-choice" name={'address-selection'} type={'checkbox'} checked={!props.checked}
        onChange={props.onChange}/><label htmlFor="DR-shipping-choice">{props.translations.misc.shippingAddressDifferent}</label>
    </div>
  </div>;
}

function CustomerTypeSelection(props) {
  return <div className={'DR-shipping-customerType-selection'}>
    <div>
      <input id="DR-shipping-customerType-choice" name={'address-customerType'} type={'checkbox'} checked={props.checked}
        onChange={props.onChange}/><label htmlFor="DR-shipping-customerType-choice">{props.translations.misc.purchaseForBusiness}</label>
    </div>
  </div>;
}

function EmailPromotionsOptInSelection(props) {
  return <div className={'DR-shipping-consents-emailPromotionsOptIn-selection'}>
    <div>
      <input id="DR-shipping-consents-emailPromotionsOptIn" name={'consents-emailPromotionsOptIn'} type={'checkbox'} checked={props.checked}
        onChange={props.onChange}/><label htmlFor="DR-shipping-consents-emailPromotionsOptIn">{props.translations.misc.marketingOptIn}</label>
    </div>
  </div>;
}

function ShippingAddressHeader(props) {
  return <AddressHeader headerText={props.translations.sectionHeading.shippingInfo} addressType={'shippingAddress'} displaySavedAddresses={true} {...props} />;
}

function CustomerInformationHeader(props) {
  return <AddressHeader headerText={props.translations.sectionHeading.customerInfo} addressType={'emailAddress'} displaySavedAddresses={false} {...props} />;
}

function AddressHeader(props) {
  return <div className={'DR-address-heading-and-button'}>
    <div className={'DR-address-heading'}>{props.headerText}</div>
    {props.displaySavedAddresses && <div>
      {props.savedAddressesExistsForUser && !props.isShowSavedAddressesMode &&
      <button id={'DR-saved-addresses-button'} className={'DR-secondary-button DR-button-small'} onClick={() => props.onClick(props.addressType, false)}>{props.translations.buttonLinkText.savedAddress}</button>}
      {props.savedAddressesExistsForUser && props.isShowSavedAddressesMode &&
      <button id={'DR-saved-addresses-back-button'} className={'DR-secondary-button DR-button-small'} onClick={() => props.onClick(props.addressType, true)}>{props.translations.buttonLinkText.back}</button>}
    </div>}
  </div>;
}

function BillingAddressHeader(props) {
  //return <div className={'DR-address-heading'}>{props.translations.sectionHeading.billingInfo}</div>;
  return <AddressHeader headerText={props.translations.sectionHeading.billingInfo} addressType={'billingAddress'} displaySavedAddresses={props.displaySavedAddresses} {...props} />;
}

class AddressElement extends Component {

  constructor(props) {
    super(props);

    this.state = {
      savedAddressType: '',
    };

  }

  handleUpdateAddress = (event, key, addressType, required, pattern) => {

    const value = event.target.value;
    if (required && value.length === 0) {
      const errors = Object.assign({}, this.props.errors);
      errors[addressType][key] = {
        code: 'LENGTH',
        message: ''
      };

      const currentAddress = this.props.getAddress(addressType);

      this.props.updateAddressAndErrors(addressType, {
        ...currentAddress,
        [key] : event.target.value
      }, errors);

      return;
    } else if (typeof pattern !== 'undefined' && !new RegExp(pattern).test(value)) {
      const errors = Object.assign({}, this.props.errors);
      let errorMsg = this.props.translations.error.validationError;
      if(key === 'postalCode'){
        errorMsg = this.props.translations.error.invalidPostalCode;
      }
      if(key === 'email'){
        errorMsg = this.props.translations.error.emailInvalid.replace('{{email}}', value);
      }
      errors[addressType][key] = {
        code: 'PATTERN',
        message: errorMsg
      };
      this.props.updateErrors(errors);
      return;
    }

    const currentAddress = this.props.getAddress(addressType);


    if (required && value.length > 0) {
      const errors = Object.assign({}, this.props.errors);
      delete errors[addressType][key];
      this.props.updateAddressAndErrors(addressType, {
        ...currentAddress,
        [key] : event.target.value
      }, errors);
    } else {
      this.props.updateAddress(addressType, {
        ...currentAddress,
        [key] : event.target.value
      });
    }


  }

  handleShippingAddressTypeChange = (event) => {
    this.props.handleShippingAddressTypeChange(!event.target.checked);
  }

  handleCustomerTypeChange = (event) => {
    this.props.handlecustomerTypeChange(event.target.checked);
  }

  handleEmailPromotionsOptInChange = (event) => {
    this.props.handleEmailPromotionsOptInChange(event.target.checked);
  }

  showSavedAddresses = (addressType, isBackBtn) => {
    if(isBackBtn){
      this.setState({
        savedAddressType: ''
      });
    }else{
      this.setState({
        savedAddressType: addressType
      });
    }
  }

  onSavedAddressChange = (index) => {

    const savedAddress = this.props.savedAddresses[index];

    const addressType = this.state.savedAddressType;

    const parsedName = parseFullName(savedAddress.name);
    
    const countryCodes = this.props.billingAddressSchema.country.enum.map(country => country.countryCode);

    const country = savedAddress?.address?.country && countryCodes.includes(savedAddress?.address?.country) ? savedAddress?.address?.country : '';

    this.setState({
      savedAddressType: ''
    });

    this.props.updateAddressAndErrors(addressType, {
      ...savedAddress,
      ...savedAddress.address,
      country: country,
      phone: savedAddress.phone,
      firstName: parsedName.first,
      lastName: parsedName.last
    }, defaultError());

  }

  render() {
    const {isShippingAddressSameAsBilling, isCustomerTypeBusinessFromCheckoutData} = this.props;
    const {savedAddressType} = this.state;
    
    const billingRequiredFields = this.props.billingRequiredFields;
    const shippingRequiredFields = this.props.shippingRequiredFields;

    const savedAddressesExistsForUser = this.props.savedAddresses?.length > 0;

    const billingAddressOnlyMode = this.props.billingAddressOnlyMode;

    const isShowSavedAddressesMode = savedAddressType.length > 0 ? true : false;
    const shoppingCountry = this.props.shoppingCountry;

    return (
      <div className={'DR-addresses-container DR-form'}>
        {!isShowSavedAddressesMode && <div>

          <CustomerInformationHeader
            translations={this.props.translations}
            savedAddressesExistsForUser={savedAddressesExistsForUser}
            isShowSavedAddressesMode={isShowSavedAddressesMode}
            onClick={this.showSavedAddresses}/>

          <AddressFromSchema addressType={'shippingAddress'}
            schema={this.props.shippingAddressSchema}
            addressOrder={this.props.emailAddressOrder}
            requiredFields={this.props.emailRequiredField}
            translations={this.props.translations}
            handleUpdateAddress={this.handleUpdateAddress}
            errors={this.props.errors}
            getAddress={this.props.getAddress} />

          <EmailPromotionsOptInSelection 
            translations={this.props.translations}
            onChange={this.handleEmailPromotionsOptInChange} />

        </div>}
        {!billingAddressOnlyMode && <ShippingAddressHeader
          translations={this.props.translations}
          savedAddressesExistsForUser={savedAddressesExistsForUser}
          isShowSavedAddressesMode={isShowSavedAddressesMode}
          onClick={this.showSavedAddresses}/>
        }

        {savedAddressesExistsForUser && isShowSavedAddressesMode &&
          <SavedAddresses savedAddresses={this.props.savedAddresses} onSavedAddressChange={this.onSavedAddressChange} shoppingCountry={shoppingCountry}/>
        }

        {!isShowSavedAddressesMode && <>

          { !billingAddressOnlyMode && <AddressFromSchema addressType={'shippingAddress'}
            schema={this.props.shippingAddressSchema}
            addressOrder={this.props.shippingAddressOrder}
            requiredFields={shippingRequiredFields}
            translations={this.props.translations}
            handleUpdateAddress={this.handleUpdateAddress}
            errors={this.props.errors}
            shoppingCountry={shoppingCountry}
            getAddress={this.props.getAddress} /> }

          <BillingAddressHeader translations={this.props.translations}
            savedAddressesExistsForUser={savedAddressesExistsForUser}
            isShowSavedAddressesMode={isShowSavedAddressesMode}
            onClick={this.showSavedAddresses}
            displaySavedAddresses={!isShippingAddressSameAsBilling || billingAddressOnlyMode}
          />

          { !billingAddressOnlyMode && <AddressTypeSelection checked={isShippingAddressSameAsBilling}
            translations={this.props.translations}
            onChange={this.handleShippingAddressTypeChange} /> }

          {(!isShippingAddressSameAsBilling || billingAddressOnlyMode) && <AddressFromSchema addressType={'billingAddress'}
            schema={this.props.billingAddressSchema}
            addressOrder={this.props.billingAddressOrder}
            requiredFields={billingRequiredFields}
            translations={this.props.translations}
            handleUpdateAddress={this.handleUpdateAddress}
            errors={this.props.errors}
            shoppingCountry={shoppingCountry}
            getAddress={this.props.getAddress}/>
          }
        </>}
        {!isCustomerTypeBusinessFromCheckoutData &&
          <CustomerTypeSelection 
            translations={this.props.translations}
            onChange={this.handleCustomerTypeChange} />
        }
        {this.props.taxInclusive && this.props.isEURowMessageDisplay && 
          <div>{this.props.translations.misc.pricesNotIncludeTaxesMsg}</div>
        }
        {this.props.errors?.main?.length > 0 && <div className={'DR-validation-error-message'}>
          {this.props.errors.main}
        </div>}

      </div>
    );
  }
}

function SavedAddresses(props) {
  return props?.savedAddresses?.map((savedAddressDetails, index) => {
    return <div className={'DR-saved-address-selection'}>
      <div className={'DR-saved-address-radio'} ><input type={'radio'} name={'DR-saved-address-input'} onClick={ () => props.onSavedAddressChange(index)} disabled={props.shoppingCountry && props.shoppingCountry !== savedAddressDetails.address.country}/></div>
      <div className={'DR-saved-address'}>
        <div className={'DR-saved-address-header'}>{savedAddressDetails.name}</div>
        <div>{savedAddressDetails.address.line1}</div>
        <div>{savedAddressDetails.address.line2}</div>
        <div>{savedAddressDetails.address.city} {savedAddressDetails.address.state} {savedAddressDetails.address.postalCode}</div>
        <div>{savedAddressDetails.address.country}</div>
      </div>
    </div>;
  });
}

function AddressFieldForSingleRow(props){
  const property = props.schema[props.fieldKey];
  const isRequired = props.requiredFields.includes(props.fieldKey);
  const translatedTitle = property['label'];
  const addressType = props.addressType;
  const currentAddress = props.getAddress(addressType);
  const value = currentAddress[props.fieldKey];
  const shoppingCountry = (props.shoppingCountry) ? props.shoppingCountry : '';

  return (
    <div className={'DR-addressOne-row'}>
      <AddressRowTitle translatedTitle={translatedTitle} required={isRequired} addressType={addressType}
        propertyKey={props.fieldKey}/>
      {!property.enum &&
      <InputField required={isRequired} property={property} propertyKey={props.fieldKey} value={value} addressType={addressType}
        translations={props.translations} errors={props.errors}
        handleUpdateAddress={props.handleUpdateAddress}/>}
      {property.enum  && property.enum.length > 0 &&
      <Select required={isRequired} property={property} propertyKey={props.fieldKey} value={value} addressType={addressType} shoppingCountry={shoppingCountry}
        translations={props.translations} errors={props.errors} handleUpdateAddress={props.handleUpdateAddress} />}
    </div>
  );
}

function AddressFieldForMultiRow(props){

  let keyFromObj = props.addressOrder.indexOf(props.fieldKey);
  return Object.values(props.addressOrder[keyFromObj]).map(keyFromObj => {
    const property = props.schema[keyFromObj];
    const isRequired = props.requiredFields.includes(keyFromObj);
    const translatedTitle = property['label'];          
    const addressType = props.addressType;
    const currentAddress = props.getAddress(addressType);
    const value = currentAddress[keyFromObj];
    const shoppingCountry = (props.shoppingCountry) ? props.shoppingCountry : '';

    let stateList = property.subdivisions;
    if(stateList){
      const newStateList = Object.values(stateList).map((subdivision) => {
        return {
          countryCode: subdivision.subdivisionCode,
          countryName: subdivision.subdivisionName,
        };
      });
      property['enum'] = newStateList;
    }
    return (
      <div>
        <div className={'DR-address-row'}>
          <AddressRowTitle translatedTitle={translatedTitle} required={isRequired} addressType={addressType}
            propertyKey={keyFromObj}/>
          {!property.enum &&
          <InputField required={isRequired} property={property} propertyKey={keyFromObj} value={value} addressType={addressType}
            translations={props.translations} errors={props.errors}
            handleUpdateAddress={props.handleUpdateAddress}/>}
          {property.enum && property.enum.length > 0 &&
          <Select required={isRequired} property={property} propertyKey={keyFromObj} value={value} addressType={addressType} shoppingCountry={shoppingCountry}
            translations={props.translations} errors={props.errors} handleUpdateAddress={props.handleUpdateAddress} />}
        </div>
      </div>
    );
  });

}

function AddressFromSchema(props) {
  return <div className={'DR-address-container'}>
    {Object.values(props.addressOrder).map(key => {   
      if(typeof key === 'string'){
        return(
          <AddressFieldForSingleRow addressType={props.addressType}
            schema={props.schema}
            requiredFields={props.requiredFields}
            translations={props.translations}
            handleUpdateAddress={props.handleUpdateAddress}
            errors={props.errors}
            shoppingCountry={props.shoppingCountry}
            getAddress={props.getAddress}
            fieldKey = {key} />
        );
      }
      else {
        return(
          <AddressFieldForMultiRow addressType={props.addressType}
            addressOrder={props.addressOrder}
            schema={props.schema}
            requiredFields={props.requiredFields}
            translations={props.translations}
            handleUpdateAddress={props.handleUpdateAddress}
            errors={props.errors}
            shoppingCountry={props.shoppingCountry}
            getAddress={props.getAddress}
            fieldKey = {key} />
        )
      }
    })}
  </div>;
}

export function appendAddress(id, options) {
  return render(<AddressElement {...options} />, id);
}