import dataStore from '../../dataStore';
import {appendShippingChoiceElement} from './ShippingChoice';
import {attachShippingChoiceToDropinCheckout} from '../checkout-sessions-api';
import {validateClientDomElement, waitForRequiredData} from '../utils';

export function shippingChoice(key, configuration, storage) {

  const {apiKey} = dataStore.get(key);

  let nodeToKeep;

  const callApiAndMount = async function (data, error) {

    const shippingMethods = data?.options?.shippingMethods;

    appendShippingChoiceElement(nodeToKeep, {
      error: error,
      locale: storage.locale,
      currency: data.currency,
      translations: storage.translations,
      shippingMethods: shippingMethods,
      selectedShippingChoice: data.shippingChoice,
      onShippingMethodSelected: async function (shippingChoiceDetails) {
        const shippingChoice = {
          'shippingChoice': shippingChoiceDetails
        };

        try {
          await attachShippingChoiceToDropinCheckout(shippingChoice, configuration.checkoutSessionId, storage.fingerprint, apiKey);
          storage.notify();
        } catch (e) {
          const errorMessage = storage.translations.error.notSaved;
          callApiAndMount(data, errorMessage);
        }
      }
    });

  };

  return {
    mount: (node) => {

      nodeToKeep = validateClientDomElement(node);

      storage.subscribers.push(callApiAndMount);

      waitForRequiredData(storage).then( () => {
        callApiAndMount(storage.checkoutData);
      });


    },

  };

}