import data from '../json/compliance.json';
import {
  getLocaleFromCountry,
  getLocaleMessage,
  getMappedLocale
} from '../app/components/localization/localized-messages';
import {getSaveCardMandateText} from './mandate';
import {isValidHttpUrl} from './url-utils';
import {removeLastCharacterFromString} from '../utils';
import {getBusinessEntityNameFromCode, getLegalLinkTypes, handleComplianceOptions, getRequiredLegalLinks} from './compliance/compliance-utils';

const defaultDRStore = 'https://store.digitalriver.com/store/defaults/';
const eCommerceProvider = '/eCommerceProvider.';
const slash = '/';


/**
 *
 * @param entityInfo {object|string} - If client passes an object, it will contain country, language, and businessEntityCode. Otherwise it will be a string containing businessEntityCode
 * @param clientLanguage {string}

 */
export function complianceGetDetails(entityInfo, clientLanguage, clientJPCommerceLawPageURL) {

  const {
    entity,
    entityName,
    encodedEntityName,
    country,
    languageLocale,
    linkLocale,
    jpCommerceLawPageUrl
  } = handleComplianceOptions(entityInfo, clientLanguage, clientJPCommerceLawPageURL);

  let complianceData = {
    disclosure: {
      businessEntity: {
        name: entityName,
        id: entity
      },
      autorenewalPlanTerms: {
        localizedText: getAutoRenewPlanTerms(languageLocale, entity, undefined, country)
      },
      saveCardMandate: {
        localizedText: getSaveCardMandateText(languageLocale, entityName)
      },
      idealRecurringAgreement: {
        localizedText: localizedText(data.keys.IDEAL_RECURRING_AGREEMENT, languageLocale, entityName, undefined, undefined, linkLocale)
      },
      confirmDisclosure: {
        localizedText: stripOutDoublePeriods(localizedText(data.keys.CONFIRM_DISCLOSURE, languageLocale, entityName, undefined, undefined, linkLocale))
      }
    }
  };

  const legalLinkTypes = getLegalLinkTypes();

  Object.entries(legalLinkTypes).forEach(([key, value]) => {
    const {localizedTextKey, gcAction, anchor} = value;

    if(getRequiredLegalLinks(country, key)) {
      complianceData.disclosure[key] = {
        localizedText: localizedText(data.keys[localizedTextKey], languageLocale, entityName, undefined, undefined, linkLocale),
      };
      if(gcAction) {
        complianceData.disclosure[key].url = localizedUrl(linkLocale, gcAction, encodedEntityName, anchor);
      }
      if(jpCommerceLawPageUrl && key === 'japaneseCommerceLaw') {
        complianceData.disclosure[key].url = jpCommerceLawPageUrl;
      }
    }
  });

  // do not like that we have an outlier here but these are based on entity not country
  if(entityInfo === 'DR_TAIWAN-ENTITY') {
    const invoiceAgreement = localizedText(data.keys.INVOICE_AGREEMENT, languageLocale);
    if(invoiceAgreement !== '') {
      complianceData.disclosure.invoiceAgreement = {
        'localizedText': invoiceAgreement
      };
    }
  }

  // should have a better way to handle this - this will be removed later when India entity page is created by Ted's team
  if(complianceData.disclosure.businessEntity.id === 'DR_INDIA-ENTITY' && complianceData.disclosure.resellerDisclosure) {
    const disclosureWithoutLinkedEntity = complianceData.disclosure.resellerDisclosure.localizedText.replace(/^<a(.+)<\/a>/, complianceData.disclosure.businessEntity.name);
    complianceData.disclosure.resellerDisclosure.localizedText = disclosureWithoutLinkedEntity;
  }

  return complianceData;

}

function hasConsents(configuration) {
  return (typeof configuration !== 'undefined' && typeof configuration.options !== 'undefined' && typeof configuration.options.consents !== 'undefined');
}

export function isCompanyNameValid(configuration) {
  return (hasConsents(configuration) && typeof configuration.options.consents.companyName === 'string');
}

export function isEulaValid(configuration) {
  return (hasConsents(configuration) && typeof configuration.options.consents.eula !== 'undefined' && typeof configuration.options.consents.eula.url === 'string' && isValidHttpUrl(configuration.options.consents.eula.url));
}

export function isTermsOfUseValid(configuration) {
  return (hasConsents(configuration) && typeof configuration.options.consents.termsOfUse !== 'undefined' && typeof configuration.options.consents.termsOfUse.url === 'string' && isValidHttpUrl(configuration.options.consents.termsOfUse.url));
}

export function localizedText(type, locale, businessEntityName, payNowButtonText, createAccountButtonTextClass, linkLocale, configuration) {
  const chosenDefaultLinkLocale = typeof linkLocale === 'undefined' ? locale : linkLocale;

  let localizedValue;
  if( (locale in data.details) && (type in data.details[locale])) {
    localizedValue = data.details[locale][type];
  } else {
    localizedValue = '';
  }
  localizedValue = evaluateVars(localizedValue, locale, businessEntityName, payNowButtonText, createAccountButtonTextClass, chosenDefaultLinkLocale, configuration);
  return localizedValue;
}

function evaluateVars(string, locale, businessEntityName, payButtonText, createAccountButtonTextClass, linkLocale, configuration) {
  const replacementRules = {
    termsOfSaleUrl: localizedUrl(linkLocale, 'DisplayDRTermsAndConditionsPage', encodeURIComponent(businessEntityName)),
    privacyPolicyUrl: localizedUrl(linkLocale, 'DisplayDRPrivacyPolicyPage', encodeURIComponent(businessEntityName)),
    resellerDisclosureUrl: localizedUrl(linkLocale,'DisplayDRAboutDigitalRiverPage', encodeURIComponent(businessEntityName)),
    businessEntityName: businessEntityName,
    payNow: typeof payButtonText !== 'undefined' ? payButtonText : getLocaleMessage(locale.split('_').join('-'), 'payNow'),
    createAccountButtonTextClass: createAccountButtonTextClass
  };
  if(isCompanyNameValid(configuration)) {
    replacementRules.companyName = configuration.options.consents.companyName;
  }
  if(isEulaValid(configuration)) {
    replacementRules.eulaUrl = configuration.options.consents.eula.url;
  }
  if(isTermsOfUseValid(configuration)) {
    replacementRules.termsOfUseUrl = configuration.options.consents.termsOfUse.url;
  }
  const replacementsPattern = /[^{}]+(?=})/g;
  const varsPattern =  /{([^}]+)}/gm;
  const replacements = string.match(replacementsPattern);
  const varsToReplace = string.match(varsPattern);


  if(varsToReplace !== null) {
    varsToReplace.forEach((stringVar, index) => {
      string = string.replace(stringVar, replacementRules[replacements[index]]);
    });
  }
  return(string);
}

function localizedUrl(locale, gcAction, entityNameEncoded, anchor) {
  const gcFormattedLocale = locale.replace('-', '_');
  let url = defaultDRStore + gcFormattedLocale + slash + gcAction + eCommerceProvider + entityNameEncoded;

  if(anchor) {
    url = `${url}#${anchor}`;
  }
  return url;
}

export function getAutoRenewPlanTerms(locale, businessEntityId, configuration, country) {
  // IMPORTANT NOTE: IF YOU CHANGE THE FORMAT OF TERMS, YOU MAY ALSO NEED TO CHANGE stripTermsHTML function in options.js - please see PR for CHECKOUTJS-2351
  const updatedLocale = locale.replace('-','_');
  const linkLocale = typeof country === 'undefined' ? updatedLocale : getLocaleFromCountry(country).replace('-', '_');
  const businessEntityName = getBusinessEntityNameFromCode(businessEntityId);
  const shouldAddPeriod = false;
  let consentsText = '';
  if(typeof configuration !== 'undefined') {
    consentsText = getConsentsText(configuration, updatedLocale, businessEntityName, shouldAddPeriod);
  }
  const autoRenewalBeginning = localizedText(data.keys.AUTORENEWAL_PLAN_TERMS_BEGIN, updatedLocale, businessEntityName, undefined, undefined, linkLocale, configuration);
  const autoRenewalEnd = localizedText(data.keys.AUTORENEWAL_PLAN_TERMS_END, updatedLocale, businessEntityName, undefined, undefined, linkLocale, configuration);
  return ( autoRenewalBeginning + consentsText + autoRenewalEnd);
}

export function stripOutDoublePeriods(text) {
  return text.split('..').join('.');
}

function isEulaOnly(configuration) {
  return(isEulaValid(configuration) && !isTermsOfUseValid(configuration));
}

function isTermsOfUseOnly(configuration) {
  return(!isEulaValid(configuration) && isTermsOfUseValid(configuration));
}

function isEulaAndTermsOfUse(configuration) {
  return(isEulaValid(configuration) && isTermsOfUseValid(configuration));
}

function getConsentsText(configuration, updatedLocale, businessEntityName, shouldAddPeriod) {
  let companyNameText;
  let eulaText;
  let termsOfUseText;
  let consentsText = '';
  let showTermsOfSaleDisclosure = false;
  let period = '.';
  if(updatedLocale === 'ja_JP' || updatedLocale === 'zh_CN' || updatedLocale === 'zh_HK' || updatedLocale === 'zh_TW') {
    period = '。';
  }

  if(typeof configuration !== 'undefined' && typeof configuration.options !== 'undefined' && typeof configuration.options.showTermsOfSaleDisclosure !== 'undefined') {
    showTermsOfSaleDisclosure = configuration.options.showTermsOfSaleDisclosure;
  }
  if(showTermsOfSaleDisclosure && isCompanyNameValid(configuration)) {
    companyNameText = localizedText(data.keys.CONSENTS_COMPANY_NAME, updatedLocale, businessEntityName, undefined, undefined, undefined, configuration);

    if(isEulaValid(configuration)) {
      eulaText = localizedText(data.keys.CONSENTS_EULA, updatedLocale, businessEntityName, undefined, undefined, undefined, configuration);
    }
    if(isTermsOfUseValid(configuration)) {
      termsOfUseText = localizedText(data.keys.CONSENTS_TERMS_OF_USE, updatedLocale, businessEntityName, undefined, undefined, undefined, configuration);
    }
    const asWellAs = localizedText(data.keys.AS_WELL_AS, updatedLocale, businessEntityName, undefined, undefined, undefined, configuration);

    if (isEulaOnly(configuration)) {
      consentsText = eulaText;
    }

    if (isTermsOfUseOnly(configuration)) {
      consentsText = termsOfUseText;
    }

    if (isEulaAndTermsOfUse(configuration)) {
      consentsText = eulaText + asWellAs + termsOfUseText;
    }

    if((typeof eulaText !== 'undefined' || typeof termsOfUseText !== 'undefined')) {
      if(['zh_HK', 'zh_TW'].includes(updatedLocale)) {
        consentsText = companyNameText + consentsText;
      } else {
        consentsText = consentsText + companyNameText;
      }
    }
  }

  if(shouldAddPeriod && consentsText !== '') {
    return consentsText + period;
  }
  else {
    return consentsText;
  }
}

export function getConfirmDisclosure(locale, businessEntityId, configuration, country) {
  const updatedLocale = locale.replace('-','_');
  const linkLocale = typeof country === 'undefined' ? updatedLocale : getLocaleFromCountry(country).replace('-', '_');
  const businessEntityName = getBusinessEntityNameFromCode(businessEntityId);
  const shouldAddPeriod = true;
  let disclosureText = localizedText(data.keys.CONFIRM_DISCLOSURE, updatedLocale, businessEntityName, undefined, undefined, linkLocale, configuration);
  const consentsText = getConsentsText(configuration, updatedLocale, businessEntityName, shouldAddPeriod);
  if(consentsText !== '') {
    // We have consents so we have to remove the period at the end of the disclosure text so we can append consents to the it. Consents already has a period at the end of it.
    disclosureText = removeLastCharacterFromString(disclosureText);
  }

  return stripOutDoublePeriods(disclosureText + consentsText);
}

export function getAgreeToTerms(locale, businessEntityId) {
  const updatedLocale = locale.replace('-','_');
  const businessEntityName = getBusinessEntityNameFromCode(businessEntityId);
  return localizedText(data.keys.AGREE_TO_TERMS, updatedLocale, businessEntityName);
}

export function getActiveAcceptance(locale, businessEntityId, paymentMethodType, buttonText, customClasses) {
  const exactLocaleMatchExists = data.details[locale.replace('-','_')].hasOwnProperty(paymentMethodType + 'ActiveAcceptance');
  const updatedLocale = exactLocaleMatchExists ? locale.replace('-','_') : getMappedLocale(locale).replace('-', '_');
  const businessEntityName = getBusinessEntityNameFromCode(businessEntityId);
  return localizedText(`${paymentMethodType}ActiveAcceptance`, updatedLocale, businessEntityName, buttonText, customClasses);
}