import { h, render, Component, Fragment } from 'preact';
import {getPlaceholderTranslationByKey} from '../address/address-translations-utils';
import {convertFileToBase64} from './taxexemption-utils';

function generateID(key) {
  return 'DR-tax-exemption-'+ key;
}

function isHighlightError(error) {
  return error?.code?.length > 0 && error?.message?.length === 0;
}

function displayValidationMessage(error) {
  return typeof error !== 'undefined' && error?.message?.length > 0;
}

function InputField({propertyKey, ...props}) {

  const error = props.errors[propertyKey];

  const inputType = {
    companyName: 'text',
    startDate: 'date',
    expirationDate: 'date',
    taxCertificate: 'file',
  };
  
  return (
    <>
      <input
        id={generateID(propertyKey)}
        name={generateID(propertyKey)}
        type={inputType[propertyKey]}
        className={isHighlightError(error) ? 'DR-validation-error-highlight DR-tax-exemption-item' : 'DR-tax-exemption-item'}
        value={(props.value || '')}
        onChange={(event) => props.handleInputChange(event, propertyKey)}
        maxLength={props.property.maxLength || ''} 
      />
      {displayValidationMessage(error) && <div className={'DR-validation-error-message'}>{error.message}</div>}
    </>
  );
}

function Select({propertyKey, ...props}) {

  const error = props.errors[propertyKey];

  return (
    <select
      className={isHighlightError(error) ? 'DR-validation-error-highlight DR-tax-exemption-item' : 'DR-tax-exemption-item'}
      id={generateID(propertyKey)}
      name={generateID(propertyKey)}
      onChange={(event) => props.handleInputChange(event, propertyKey, props.required)}
      value={props.value || ''}
    >
      <option value={''}>{props.translations.misc.select}</option>
      {props.property.enum.map(e => {
        return <option value={e.shortCode}>{e.name}</option>;
      })}
    </select>
  );
}

function TaxExemptionRowTitle(props) {
  return (
    <label htmlFor={generateID(props.propertyKey)}>{props.translatedTitle} {props.required && <span>&#42;</span>}</label>
  );
}

class TaxExemptionElement extends Component {
  constructor(props) {
    super(props);
  }

  handleInputChange = async (event, key) => {

    const currentTaxCertificate = this.props.getTaxCertificate();

    if (key === 'taxCertificate') {
      const fileInput = event.target;

      if (fileInput.files && fileInput.files[0]) {
        const selectedFile = fileInput.files[0];
        const filename = selectedFile.name;
        const allowedExtensions = /(\.jpg|\.jpeg|\.png|\.pdf)$/i;
        const errors = Object.assign({}, this.props.errors);

        if (!allowedExtensions.exec(filename)) {
          fileInput.value = '';
          errors[key] = {
            code: 'FILETYPE',
            message: this.props.translations.error.invalidFileType
          };
          this.props.updateErrors(errors);
          return;
        }

        if (errors[key]) {
          delete errors[key];
          this.props.updateErrors(errors);
        }

        const base64 = await convertFileToBase64(selectedFile);

        this.props.updateTaxCertificate({
          ...currentTaxCertificate,
          [key]: filename,
          base64: base64,
          filename: filename
        });
      }
    } else {
      const inputValue = event.target.value;
      const errors = Object.assign({}, this.props.errors);
      const today = new Date();

      switch (key) {

        case 'startDate':

          const startDate = new Date(inputValue);

          if (startDate > today) {
            errors[key] = {
              code: 'DATE',
              message: this.props.translations.error.invalidStartDate
            };
            this.props.updateErrors(errors);
          } else {
            delete errors[key];
            this.props.updateTaxCertificateAndErrors({
              ...currentTaxCertificate,
              [key]: inputValue
            }, errors);
          }

          break;

        case 'expirationDate':

          const expirationDate = new Date(inputValue);

          if (expirationDate < today) {
            errors[key] = {
              code: 'DATE',
              message: this.props.translations.error.invalidEndDate
            };
            this.props.updateErrors(errors);
          } else {
            delete errors[key];
            this.props.updateTaxCertificateAndErrors({
              ...currentTaxCertificate,
              [key]: inputValue
            }, errors);
          }

          break;

        case 'companyName':
        case 'certificateTaxAuthority':

          if (inputValue) {
            delete errors[key];
            this.props.updateTaxCertificateAndErrors({
              ...currentTaxCertificate,
              [key]: inputValue
            }, errors);
          }

          break;
      }
    }
  }

  render() {
    return (
      <div className={'DR-tax-exemption-container'}>
        <div className={'DR-tax-exemption-heading'}>
          {this.props.translations.sectionHeading.taxExemption}
        </div>
        <TaxExemption
          schema={this.props.taxExemptionSchema}
          requiredFields={this.props.taxExemptionRequiredFields}
          translations={this.props.translations}
          errors={this.props.errors}
          handleInputChange={this.handleInputChange}
          getTaxCertificate={this.props.getTaxCertificate}
        />
        {this.props.errors?.main?.length > 0 &&
          <div className={'DR-validation-error-message'}>
            {this.props.errors.main}
          </div>
        }
      </div>
    );
  }
}

function TaxExemption(props) {
  return (
    <div className={'DR-tax-exemption-fields DR-form'}>
      {Object.keys(props.schema).map(key => {
        const property = props.schema[key];
        const isRequired = props.requiredFields.includes(key);
        const translatedTitle = getPlaceholderTranslationByKey(key, props.translations);
        const currentTaxCertificate = props.getTaxCertificate();
        const value = currentTaxCertificate[key];

        return (
          <div className={'DR-tax-exemption-row'}>
            <TaxExemptionRowTitle translatedTitle={translatedTitle} required={isRequired} propertyKey={key} />
            {property.enum && property.enum.length > 1 &&
              <Select
                required={isRequired}
                property={property}
                propertyKey={key}
                translations={props.translations}
                errors={props.errors}
                handleInputChange={props.handleInputChange}
                value={value}
              />
            }
            {!property.enum &&
              <InputField
                required={isRequired}
                property={property}
                propertyKey={key}
                translations={props.translations}
                errors={props.errors}
                handleInputChange={props.handleInputChange}
                value={value}
              />
            }
          </div>
        );
      })}
    </div>
  );
}

export function appendTaxExemption(id, options) {
  return render(<TaxExemptionElement {...options} />, id);
}