import { h, render, Component }  from 'preact';

class ComplianceElement extends Component {

  render() {

    return (
      <div className={'DR-Legal-Footer'}>

        <div id={'DR-Legal-Footer-Links'}>

          {this.props.links.map(link => {
            return <span>
              <a target={'_blank'} className={'DR-Legal-Footer-Link'} href={link.url} >{link.name}</a>
            </span>;
          })}

        </div>

      </div>
    );
  }
}



export function appendCompliance(id, options) {
  return render(<ComplianceElement {...options} />, id);
}