import {csvLength} from '../validator';

/**
 * Converts a number into a count to that number, e.g. 4 becomes 1234
 * @param lengthOfCVV
 * @param value
 * @param index
 * @returns {string|string|*}
 */
export function convertLengthToPlaceholderText(lengthOfCVV, value = '', index = 1) {
  if (index > lengthOfCVV) {
    return value;
  }
  return convertLengthToPlaceholderText(lengthOfCVV, value + index, ++index);
}

export function getLengthOfCVV(event) {
  return event.brand !== 'unknown' ? csvLength(event.brand) : 3;
}