import {getComponentsBasePath} from './createComponent';

export function getRedirectReceiverURL(controllerId) {
  return `${getComponentsBasePath()}/redirect-receiver/redirect-receiver.html?controllerId=${controllerId}&componentId=redirect-receiver-1757f835-c21b-4f4e-b208-92adc5c30573`;
}

export function isValidHttpUrl(string) {
  let url;
  try {
    url = new URL(string);
  } catch (_) {
    return false;
  }
  return url.protocol === 'http:' || url.protocol === 'https:';
}