export function getPlaceholderTranslationByKey(key, translations) {

  const translation = translations.placeholder[mapAddressTranslationKey(key)];

  if (typeof translation === 'undefined') {
    return key;
  }

  return translation;
}

export function mapAddressTranslationKey(key) {
  const mappings = {
    line1: 'addressLine1',
    line2: 'addressLine2',
    organization: 'companyName'
  };

  const mappingFound = mappings[key];
  if (typeof mappingFound !== 'undefined') {
    return mappingFound;
  } else {
    return key;
  }
}