export function countryHasEntity(country) {
  const entitiesForCountry = getEntityForCountry(country);
  return typeof entitiesForCountry !== 'undefined' && entitiesForCountry.length > 0;
}

export function getEntityForCountry(country) {
  return defaultEntitysCountries()[country];
}

export function runGetCountrySpecsAndHandleResponse(apiKey, country, sellingEntity) {

  if (typeof sellingEntity === 'undefined' && countryHasEntity(country)) {
    sellingEntity = getEntityForCountry(country)[0];
  }

  return paymentServiceGetRequest(apiKey, `${config.apiUrl}/country-specs?country=${country}&sellingEntity=${sellingEntity}`).then((response) => {
    return response.data.data;
  })
    .catch(() => {
      return [];
    });
}
