import {
  attachSourceToDropinCheckout,
  attachStoreCreditToDropinCheckout,
  createDropinCheckoutOrder,
} from '../checkout-sessions-api';
import {sendRedirectSourceData} from '../../dropin-events';
import {getComponentWindow} from '../../createComponent';
import postRobot from '../../../post-robot-wrapper';
import {config} from '../../../app/components/config';
import {isStoreCredit} from '../../dropin/stored-payment-methods';

function isStoredCreditCard(type) {
  return type === 'creditCard';
}

async function handleAttachSourceSource(storage) {
  if (isStoreCredit(storage.data?.storedPaymentMethod?.type)) {
    const upstreamId = storage.data.storedPaymentMethod.id;
    return attachStoreCreditToDropinCheckout(storage.checkoutSessionId, upstreamId, storage.fingerprint, storage.apiKey);
  } else if (isStoredCreditCard(storage.data?.storedPaymentMethod?.type)) {
    return attachSourceToDropinCheckout(storage.checkoutSessionId, storage.data.storedPaymentMethod.id, storage.fingerprint, storage.apiKey);
  } else {
    const saveForLater = storage.data.readyForStorage === true;
    return attachSourceToDropinCheckout(storage.checkoutSessionId, storage.data.source.id, storage.fingerprint, storage.apiKey, saveForLater);
  }
}

export async function handleAfterSourceCreated(configuration, storage, apiKey) {

  try {
    const sourceResponse = await handleAttachSourceSource(storage);
    if (sourceResponse.status === 409) {
      return Promise.resolve({
        responseCode: sourceResponse.status,
        status: 'RETRY'
      });
    }

    if (sourceResponse.status === 400) {
      return Promise.resolve({
        responseCode: sourceResponse.status,
        status: 'STOP'
      });
    }

    const source = await sourceResponse.json();

    // NOT SURE WHEN THIS COULD HAPPEN
    /*if (source.error || !source.id) {
      console.log('Detaching source', sourceResponse);
      await detachSourceToDropinCheckout(configuration.checkoutSessionId, storage.data.source.id, storage.fingerprint, apiKey);
    }*/

    // Create order
    const orderResponse = await createDropinCheckoutOrder(configuration.checkoutSessionId, storage.fingerprint, apiKey);
    const order = await orderResponse.json();

    if (orderResponse.status === 409 && order?.errors?.length > 0 &&  order.errors[0].code === 'additional_payment_action_required') {
      return Promise.resolve({
        responseCode: orderResponse.status,
        status: '3DS2',
        order: order
      });
    }

    if (orderResponse.status === 409) {
      return Promise.resolve({
        responseCode: orderResponse.status,
        status: 'BAD_ORDER',
        order: order
      });
    }

    if (orderResponse.status === 400) {
      return Promise.resolve({
        responseCode: orderResponse.status,
        status: 'BAD_ORDER'
      });
    }

    if (order.error) {
      return Promise.resolve({
        responseCode: orderResponse.status,
        status: 'BAD_ORDER'
      });
    }

    if (orderResponse.status !== 201) {
      return Promise.resolve({
        responseCode: orderResponse.status,
        status: 'RETRY',
      });
    }

    return {
      order: order,
      source: source
    };
  } catch (e) {
    return Promise.resolve({
      status: 'RETRY',
    });
  }

}

export function handleRedirectComplete(controller, storage, dropinOnCancel) {
  const controllerWindow = getComponentWindow(controller.id);
  postRobot.on('redirectComplete', {window: controllerWindow, domain: config.domain}, (event) => {
    const {action} = event.data;
    if (action === 'return') {
      storage.complete(storage.orderCreationData);
      storage.notify(storage.checkoutData);
    } else {
      dropinOnCancel(storage.orderCreationData);
    }
  });
}

export function handleRedirectResponse(controller, storage) {
  const timeout = setInterval(() => {
    const windowOpenerURL = storage.windowOpenerURL;
    if (typeof windowOpenerURL !== 'undefined' && windowOpenerURL !== '') {
      clearTimeout(timeout);
      storage.window.location.replace(windowOpenerURL);
      sendRedirectSourceData(controller.id, storage.data.source.id, storage.data.source.clientSecret.split('_')[1], storage.window, storage.selectedPaymentMethodType);
    }
  }, 200);

  return timeout;
}