import {
  chooseCreateSourceCatchMessage,
  runCreateSourceAndHandleResponse
} from './controller-create-source-utils';

export function handleMSTSCreateSource(mstsComponent, sourceRequest, apiKey) {

  const mstsComponentPromise = mstsComponent.send('getMstsComponentData'+mstsComponent.id, {});
  return mstsComponentPromise
    .then((response) => {
      const data = response.data;
      sourceRequest.msts = Object.assign({}, sourceRequest.msts, data);
      return runCreateSourceAndHandleResponse(sourceRequest, apiKey);

    }).catch(function (error) {
      return chooseCreateSourceCatchMessage(error);
    });
}