export function createStorage(apiKey, controller, instanceOptions, configuration) {

  const storage = { // TODO Think about changing this to a class....
    checkoutSessionId: configuration.checkoutSessionId,
    apiKey: apiKey,
    controller: controller,
    locale: instanceOptions.locale,
    subscribers: [],
    extraData: {},
    selectedPaymentMethod: {
      getType: function () {
        const selectedPaymentMethod = document.querySelector('.DR-show');
        return selectedPaymentMethod.getAttribute('data-type');
      },
      getLocalizedName: function () {
        return document.querySelector('.DR-card-header h5 button[aria-expanded=\'true\'] span').innerHTML;
      }
    }
  };

  storage.complete = createCompleteEvent(configuration, storage);

  storage.fireClientOnChange = function () {
    if(configuration.onChange) {
      configuration.onChange({
        ...storage.extraData,
        ...storage.checkoutData
      });
    }
  };

  storage.notify = async function (checkoutData) {

    if (checkoutData) {
      storage.subscribers.forEach(s => {
        s(checkoutData);
      });
    } else {

      const checkoutResponse = await getDropinCheckout(configuration.checkoutSessionId, apiKey, storage.fingerprint);

      if (checkoutResponse.status === 200) {
        const checkout = await checkoutResponse.json();
        storage.checkoutData = checkout;
        storage.subscribers.forEach(s => {
          s(checkout);
        });
        storage.fireClientOnChange();
      }
    }
  };



  return storage;
}