import {paymentServiceGetRequest} from '../../payment-service-request';
import {config} from '../config';

export function runGetAmazonRegion(apiKey, country, currency) {
  return paymentServiceGetRequest(apiKey, `${config.paymentServiceBaseUrl}/amazonPayExpress/preAuth?country=${country}&currency=${currency}`)
    .then((response) => {
      return response.data;
    }).catch((errorResponse) => {
      const error = {
        data: {
          error: {
            message: errorResponse.message
          },
          source: null
        }
      };
      return error;
    });
}