import {createSourceData, getPayPalDefaultStyle,} from '../../create-dropin';
import {getLocaleMessage} from '../../../app/components/localization/localized-messages';
import DigitalRiverPaymentRequest from '../../DigitalRiverPaymentRequest';


function createPaymentRequest(locale, paymentSessionId, checkoutData, sessionInformation) {
  const orderTotalLabel = getLocaleMessage(locale, 'orderTotal');
  const style = {
    buttonType: 'plain',
    buttonColor: 'dark',
    buttonLanguage: 'en'
  };

  return new DigitalRiverPaymentRequest({
    sessionId: paymentSessionId,
    country: checkoutData.locale.split('_')[1],
    currency: sessionInformation.currency,
    total: {
      label: orderTotalLabel,
      amount: parseFloat(sessionInformation.amountRemainingToBeContributed),
    },
    shippingOptions: getShippingMethods(checkoutData, locale),
    style: style,
    requestShipping: checkoutData.requiresShipping
  });
}

function getShippingMethods(checkoutData, locale) {
  return checkoutData.options?.shippingMethods?.map((sm, index) => {
    return {
      id: index.toString(),
      label: sm.description,
      amount: sm.amount,
      selected: checkoutData?.shippingChoice.description === sm.description,
      detail: new Intl.NumberFormat(locale, { style: 'currency', currency: checkoutData.currency }).format(sm.amount)
    };
  });
}

export function GooglePayComponent(props) {

  const container = document.createElement('div');
  container.id = 'dr-container-' + props.paymentMethod.type;

  props.parent.appendChild(container);

  const paymentRequest = createPaymentRequest(props.locale, props.paymentSessionId, props.checkoutData, props.paymentMethodResponse.sessionInformation);

  const component = props.createElement('googlepay', paymentRequest);

  if (component.canMakePayment()) {
    component.mount(container.id);
    return {
      componentInstance: component,
    };
  } else {
    return null;
  }

}

