export const prettyNumber = (num) => {
  return +(num).toFixed(12);
};

export function PayPalComponent(props) {

  const {controllerId, paymentMethod, paymentMethodResponse, locale, availablePaymentMethod, checkoutData} = props;

  const container = document.createElement('div');
  container.id = 'dr-container-' + props.paymentMethod.type;

  props.parent.appendChild(container);
  const config = {
    sessionId: props.paymentSessionId
  };

  const paymentRequest = {
    style: getPayPalDefaultStyle(),
    sourceData: createSourceData(config, paymentMethod, controllerId, availablePaymentMethod, paymentMethodResponse.sessionInformation, locale)
  };

  paymentRequest.sourceData.payPal.requestShipping = checkoutData.requiresShipping;
  paymentRequest.sourceData.payPal.amountEstimated = true;

  paymentRequest.sourceData.payPal.items = checkoutData?.items?.map(item => {
    return {
      name: item.sku.name,
      quantity: item.quantity,
      unitAmount: item.amount
    };
  });

  if (checkoutData.requiresShipping) {
    paymentRequest.sourceData.payPal.shippingAmount = checkoutData.taxInclusive ? prettyNumber((checkoutData.shippingChoice?.amount + checkoutData.shippingChoice?.taxAmount)) : checkoutData.totalShipping;
  }

  paymentRequest.sourceData.payPal.taxAmount = checkoutData.totalTax;

  const component = props.createElement('paypal', paymentRequest);

  component.mount(container.id);

  return {
    componentInstance: component,
  };

}

export function paymentMethodDetails() {
  return [
    {name: 'Google Pay', code: 'googlepay', type: 'googlePay', component: GooglePayComponent},
    {name: 'Apple Pay', code: 'applepay', type: 'applePay', component: ApplePayComponent},
    {name: 'PayPal', code: 'paypal', type: 'payPal', component: PayPalComponent},
  ];
}
