import {
  chooseCreateSourceCatchMessage,
  formatComponentTriggerErrors,
  handleCreateSourceValidation, runCreateSourceAndHandleResponse
} from './controller-create-source-utils';

function createCreditCardData(sourceRequest, creditCardNumber, expirySplit, creditCardCVV) {
  return Object.assign({}, sourceRequest.creditCard, {
    number: creditCardNumber,
    expirationMonth: parseInt(expirySplit[0]),
    expirationYear: parseInt(20 + expirySplit[1]), // Appends 20 to the yy format
    cvv: creditCardCVV
  });
}

export function retrieveKoreanCardDetails(dataFromComponent) {

  const birthdate = dataFromComponent.birthdate;
  const cardPassword = dataFromComponent.cardPassword;
  const corporateRegistrationNumber = dataFromComponent.corporateRegistrationNumber;

  return {
    birthdate: birthdate,
    cardPassword: cardPassword,
    corporateRegistrationNumber: corporateRegistrationNumber
  };

}

function createDataRetrievalPromises(creditCardNumberComponent, creditCardExpiryComponent, creditCardCVVComponent, koreanCardComponent) {
  const creditCardNumberPromise = creditCardNumberComponent.send('getComponentData', {});
  const creditCardExpiryPromise = creditCardExpiryComponent.send('getComponentData', {});
  const creditCardCVVPromise = creditCardCVVComponent.send('getComponentData', {});
  if (koreanCardComponent) {
    const koreanCardPromise = koreanCardComponent.send('getComponentData', {});
    return [creditCardNumberPromise, creditCardCVVPromise, creditCardExpiryPromise, koreanCardPromise];
  } else {
    return [creditCardNumberPromise, creditCardCVVPromise, creditCardExpiryPromise];
  }
}

function isKoreanCardDataReturned(values) {
  return values[3];
}

export function handleCreditCardCreateSource(creditCardNumberComponent, creditCardCVVComponent, creditCardExpiryComponent, koreanCardComponent, sourceRequest, apiKey, redirectReceiverReturnURL) {

  if (!creditCardNumberComponent || !creditCardCVVComponent || !creditCardExpiryComponent) {
    return Promise.reject('Cannot create source because all the required components have not been created for a credit card submission');
  }

  if (!creditCardNumberComponent.send || !creditCardCVVComponent.send || !creditCardExpiryComponent.send) {
    return Promise.reject('Cannot create source because all the required components have not been mounted for a credit card submission');
  }

  const creditCardGetComponentDataPromises = createDataRetrievalPromises(creditCardNumberComponent, creditCardExpiryComponent, creditCardCVVComponent, koreanCardComponent);

  return Promise.all(creditCardGetComponentDataPromises)
    .then((componentValues) => {

      const creditCardNumber = componentValues[0].data;
      const creditCardCVV = componentValues[1].data;
      const creditCardExpiry = componentValues[2].data;

      // trigger event so that change events with errors get sent

      const triggerCCNumEvent = creditCardNumberComponent.send('componentTriggerEvent', {type: 'createSource'});
      const triggerCCExpireEvent = creditCardExpiryComponent.send('componentTriggerEvent', {type: 'createSource'});
      const triggerCVVEvent = creditCardCVVComponent.send('componentTriggerEvent', {type: 'createSource'});

      const ccTriggerEvents = [triggerCCNumEvent, triggerCCExpireEvent, triggerCVVEvent];

      return Promise.all(ccTriggerEvents).then((triggerValues) => {

        const errors = formatComponentTriggerErrors(triggerValues);

        if (errors.length > 0) {
          return handleCreateSourceValidation(errors);
        }

        const expirySplit = creditCardExpiry.split('/'); // Assumes the expiry is in MM/yy format

        sourceRequest.creditCard = createCreditCardData(sourceRequest, creditCardNumber, expirySplit, creditCardCVV);

        if(isKoreanCardDataReturned(componentValues)) {
          const koreanCardDataFromComponent = retrieveKoreanCardDetails(componentValues[3].data);
          sourceRequest.creditCard.birthdate = koreanCardDataFromComponent.birthdate;
          sourceRequest.creditCard.cardPassword = koreanCardDataFromComponent.cardPassword;
          sourceRequest.creditCard.corporateRegistrationNumber = koreanCardDataFromComponent.corporateRegistrationNumber;
        }

        if (typeof sourceRequest.creditCard.returnUrl === 'undefined') {
          sourceRequest.creditCard.returnUrl = redirectReceiverReturnURL;
        }

        return runCreateSourceAndHandleResponse(sourceRequest, apiKey);

      }).catch(function (error) {
        return chooseCreateSourceCatchMessage(error);
      });


    }).catch(function (error) {
      return chooseCreateSourceCatchMessage(error);
    });
}