import { h, render, Component }  from 'preact';
import {getComponentsBasePath} from '../../createComponent';

class ThankYouElement extends Component {

  render() {

    const pathToCheckmark = getComponentsBasePath() + '/offline-refund';

    return (
      <div className={'DR-thank-you-container'}>

        {this.props.orderDetails?.id && <div>
          <h1>{this.props.translations.sectionHeading.thankYou}</h1>
          <div className={'DR-thank-you-content'}>
            <div><img src={pathToCheckmark + '/checkmark.svg'} style="height: 50px; width: 50px"/></div>
            <div><h2>{this.props.translations.misc.thankYouMsg1}</h2></div>
            <div>{this.props.translations.misc.thankYouMsg2 + ' #' + this.props.orderDetails.id}</div>
            <div>{this.props.translations.misc.thankYouMsg3}</div>
          </div>
        </div>}

        <div id={'DR-delayed-payment-instructions'}>

        </div>

      </div>
    );
  }
}



export function appendThankYou(id, options) {
  return render(<ThankYouElement {...options} />, id);
}