import {paymentServiceGetRequest} from '../../payment-service-request';
import {config} from '../config';
import {getLanguageFromLocale} from './controller-language';


/**
 * Gets the payment methods
 * @param apiKey
 * @param sessionId
 * @param currency
 * @param country
 * @param locale
 * @param supportsStorage
 * @param recurringModel
 * @param supportsRecurring
 * @param supportsFreeTrial
 * @returns {Promise<Array> | * | Promise<T | Array>}
 */

export function runGetPaymentMethodsAndHandleResponse(apiKey, sessionId, currency, country, locale, supportsStorage, recurringModel, supportsRecurring, supportsFreeTrial) {

  const sessionIdParam  = createParam('sessionId', sessionId);
  const currencyParam  = createParam('currency', currency);
  const countryParam  = createParam('country', country);
  const localeParam = createParam('locale', locale ? locale.toLowerCase() : null);
  const languageParam = createParam('language', locale ? getLanguageFromLocale(locale) : null);
  const supportsFreeTrialParam = createParam('supportsFreeTrial', supportsFreeTrial);
  const supportsRecurringParam = createParam('supportsRecurring', supportsRecurring);
  const supportsStorageParam  = createParam('supportsStorage', supportsStorage);
  const recurringModelParam = createParam('recurringModel', recurringModel);

  const paramURL = paramBuilder([sessionIdParam, currencyParam, countryParam, localeParam, supportsFreeTrialParam, supportsRecurringParam, supportsStorageParam, recurringModelParam, languageParam]);

  const url = `${config.paymentMethodsUrl}${paramURL}`;

  return paymentServiceGetRequest(apiKey, url)
    .then(handleSuccessResponse)
    .catch(handleErrorResponse);
}

function createParam(name, value) {
  return value !== null && typeof value !== 'undefined' && (value.length > 0 || typeof value === 'boolean') ? name+'='+value : '';
}

function paramBuilder(params) {

  const validParams = params.filter( param => param.length > 0);

  if (validParams.length === 0) {
    return '';
  } else {
    return '?'+validParams.join('&');
  }

}


function handleSuccessResponse(response) {
  return response.data;
}

function handleErrorResponse() {
  return [];
}