import {
  attachAddressToDropinCheckout,
  attachShippingChoiceToDropinCheckout,
  getDropinCheckout
} from '../checkout-sessions-api';
import {showSpinnerOverlay} from '../payment/payment-events';
import {handleAfterSourceCreated} from '../payment/payment-api-call';

export function createProcessSourceData(storage, callApiAndMount, configuration) {
  return async function (sourceData) {

    storage.data = sourceData;

    callApiAndMount(storage.checkoutData);

    try {

      if (sourceData.shippingAddress) {
        const address = {
          email: sourceData?.contactInformation?.email,
          shipTo: getAddressFromSourceData(sourceData.shippingAddress),
        };
        await attachAddressToDropinCheckout(address, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);
      }

      if (sourceData.elementType === 'paypal') {
        showSpinnerOverlay();
      }

      const data = await handleAfterSourceCreated(configuration, storage, storage.apiKey);

      if (data.status) {
        callApiAndMount(storage.checkoutData, storage.translations.error.notSaved);
        return {
          status: false
        };
      }

      storage.orderCreationData = data;

      const order = data.order;
      storage.complete({
        order: order
      });

      storage.notify(storage.checkoutData);

      return {
        status: true
      };

    } catch (e) {
      return {
        status: false
      };
    }

  };
}

export function createAttachShippingChoiceToCheckoutSession(checkoutData, configuration, storage) {
  return async function (id) {
    const shippingChoice = {
      'shippingChoice': checkoutData.options.shippingMethods[id]
    };

    try {
      await attachShippingChoiceToDropinCheckout(shippingChoice, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);
      const checkout = await getDropinCheckout(configuration.checkoutSessionId, storage.apiKey, storage.fingerprint).then(data => data.json());

      return {
        status: true,
        data: checkout
      };

    } catch (e) {
      return {
        status: false,
      };
    }
  };
}

export function createAttachShippingAddressToCheckoutSession(configuration, storage) {
  return async function (address) {
    try {

      const response = await attachAddressToDropinCheckout(address, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);

      const checkoutResponse = await getDropinCheckout(configuration.checkoutSessionId, storage.apiKey, storage.fingerprint);

      const checkout = await checkoutResponse.json();

      return {
        status: response.status === 200 && checkoutResponse.status === 200,
        data: checkout
      };

    } catch (e) {
      return {
        status: false
      };
    }
  };
}

function getAddressFromSourceData(addressFromSource) {

  return {
    email: addressFromSource.email,
    phone: addressFromSource.phone,
    name: addressFromSource.firstName + ' ' + addressFromSource.lastName,
    address: {
      line1: addressFromSource.address.line1,
      line2: addressFromSource.address.line2,
      city: addressFromSource.address.city,
      state: addressFromSource.address.state,
      country: addressFromSource.address.country,
      postalCode: addressFromSource.address.postalCode
    }
  };
}