import {
  chooseCreateSourceCatchMessage,
  formatComponentTriggerErrors,
  handleCreateSourceValidation, runCreateSourceAndHandleResponse
} from './controller-create-source-utils';

function createDataRetrievalPromise(component) {
  const componentPromise = component.send('getComponentData', {});
  return componentPromise;
}

export function handleGiftCardCreateSource(giftCardNumberComponent, giftCardPinComponent, sourceRequest, apiKey) {
  if (!giftCardNumberComponent || !giftCardPinComponent) {
    return Promise.reject('Cannot create source because all the required components have not been created for a gift card submission');
  }

  if (!giftCardNumberComponent.send || !giftCardPinComponent.send) {
    return Promise.reject('Cannot create source because all the required components have not been mounted for a gift card submission');
  }

  const giftCardNumberPromise = createDataRetrievalPromise(giftCardNumberComponent);
  const giftCardPinPromise = createDataRetrievalPromise(giftCardPinComponent);
  const giftCardPromises = [giftCardNumberPromise, giftCardPinPromise];


  return Promise.all(giftCardPromises).then((values) => {
    const [giftCardNumberElement, giftCardPinElement] = values;
    const giftCardNumber = giftCardNumberElement.data;
    const giftCardPin = giftCardPinElement.data;
    // trigger event so that change events with errors get sent

    const triggerGiftCardEvent = giftCardNumberComponent.send('componentTriggerEvent', {type: 'createSource'});

    return triggerGiftCardEvent.then((triggerValues) => {
      const errors = formatComponentTriggerErrors([triggerValues]);

      if (errors.length > 0) {
        return handleCreateSourceValidation(errors);
      }

      sourceRequest.giftCard = {
        number: giftCardNumber,
        pin: giftCardPin
      };

      return runCreateSourceAndHandleResponse(sourceRequest, apiKey);
    }).catch(function (error) {
      return chooseCreateSourceCatchMessage(error);
    });
  }).catch(function (error) {
    return chooseCreateSourceCatchMessage(error);
  });
}