import {
  destroy,
  generateComponentId,
  getComponentWindow,
  onEventHandler,
  unmount,
  //update
} from '../createComponent';

import {sanitizeOptionsForType} from '../../app/components/options';
import dataStore from '../dataStore';
import postRobot from '../../post-robot-wrapper';
import {config} from '../../app/components/config';
import {sendEventData} from '../../app/components/input-events';
import {getPaymentMethods} from '../fetch-payment-methods';

const COMPONENT_TYPE = 'amazonpay';
const START_BUTTON_ID = 'DR-amazon-pay-start-button';
const FINAL_BUTTON_ID = 'DR-amazon-pay-final-button';
const AMAZON_SCRIPT_SRC = {
  na: 'https://static-na.payments-amazon.com/checkout.js',
  eu: 'https://static-eu.payments-amazon.com/checkout.js',
  jp: 'https://static-fe.payments-amazon.com/checkout.js'
};
const AMAZON_CHECKOUT_LANGUAGES = ['en_US', 'en_GB', 'de_DE', 'fr_FR', 'it_IT', 'es_ES', 'js_JP'];


let controllerEmitter;
let componentData = {};

/**
 * Creates Amazon Pay element
 * @param {string} controllerId
 * @param {string} key
 * @param {object} options
 * @returns {{unmount: unmountElement, controllerId, options: (*|{}), destroy: amazonPay.destroy, id: string, parentNode: null, window: {prototype: Window, new(): Window}, type: string, mount: ((function(*): *)|*), key, on: onEventHandler}}
 */
export function createAmazonPay(controllerId, key, options) {
  if(!options || !options.sourceData) {
    throw new Error('You must provide an options object with a sourceData object for this element');
  }
  if(!options.sourceData.sessionId) {
    throw new Error('You must provide sessionId in the sourceData object');
  }
  if(!options.sourceData.amazonPay) {
    throw new Error('You must provide an amazonPay object in the sourceData object');
  }
  if(!options.sourceData.amazonPay.returnUrl) {
    throw new Error('You must provide a returnUrl in the amazonPay object');
  }
  if(!options.sourceData.amazonPay.cancelUrl) {
    throw new Error('You must provide a cancelUrl in the amazonPay object');
  }
  if(!options.sourceData.amazonPay.resultReturnUrl) {
    throw new Error('You must provide a resultReturnUrl in the amazonPay object');
  }
  const id = generateComponentId(COMPONENT_TYPE);

  const amazonPay = {
    id: id,
    key: key,
    type: COMPONENT_TYPE,
    parentNode: null,
    controllerId: controllerId,
    mount: mountElement,
    destroy: destroy,
    on: onEventHandler,
    options: sanitizeOptionsForType(options, COMPONENT_TYPE),
    unmount: unmount,
    //update: updateElement,
    window: Window
  };

  componentData = {
    componentType: COMPONENT_TYPE,
    componentId: id,
    controller: {
      id: controllerId,
      window: getComponentWindow(controllerId)
    },
    options: amazonPay.options,
    key: key
  };

  controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });
  return amazonPay;
}

/**
 * Mounts Amazon Pay element
 * @param {string} parentNodeId
 */
export function mountElement(parentNodeId) {
  if (this.parentNode !== null) {
    throw new Error(`Can not mount, only one ${this.type} element can be mounted at a time.`);
  }

  let parent = typeof parentNodeId === 'string' ? document.getElementById(parentNodeId) : parentNodeId;

  if (!(parent instanceof HTMLElement)) {
    throw new Error('mount() requires a valid HTMLElement or id attribute.');
  }

  componentData.window = window;
  const data = dataStore.get(this.key);

  data.components[this.type] = {'parent': parent, 'options': sanitizeOptionsForType(this.options, this.type)};
  dataStore.set(this.key, data);

  this.parentNode = parent;


  componentData.parentNode = parent;

  return controllerEmitter.send('mountClientComponent', {
    componentId: componentData.componentId,
    componentType: componentData.componentType
  }).then((response) => {
    handleOptions(response.data);
  });

}


/**
 * Adds options to componentData, adds button
 * @param data
 */
export async function handleOptions(data) {
  componentData.instanceOptions = data.instanceOptions;
  componentData.options = data.options;
  const {componentId, parentNode} = componentData;
  addContainerToDom(parentNode, componentId);
  await addButton();
  if(componentData.ledgerCurrency) {
    emitComponentReady();
  } else {
    const eventData = {
      'error': {
        'liveMode': false,
        'type': 'bad_request',
        'errors': [
          {
            'code': 'missing_parameter',
            'parameter': 'country',
            'message': 'Country must be specified in session or in options'
          }
        ]
      },
    };
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'ready', eventData);
  }

}

/**
 * Adds Amazon Pay container element to dom in parentNode
 * @param parentNode
 * @param componentId
 */
function addContainerToDom(parentNode, componentId) {
  parentNode.innerHTML = `<div id="${componentId}"><div class="DR-amazon-pay-button" id="${START_BUTTON_ID}"></div><div class="DR-amazon-pay-button" id="${FINAL_BUTTON_ID}"></div></div>`;
}

/**
 * Adds Amazon Pay button
 * @param options
 */
export function addButton() {
  let amazonPayScript = document.getElementById('DR-amazon-pay');
  return getAmazonRegionalData(componentData).then((regionalData) => {
    const {localizedScript, ledgerCurrency} = regionalData;
    if(ledgerCurrency) {
      componentData.ledgerCurrency = ledgerCurrency;
      if(!document.getElementById('DR-amazon-pay')) {
        amazonPayScript = document.createElement('script');
        amazonPayScript.src = localizedScript;
        amazonPayScript.id = 'DR-amazon-pay';
        document.body.appendChild(amazonPayScript);
        amazonPayScript.addEventListener('load', renderAmazonButton);
      } else {
        renderAmazonButton();
      }
    }

  });

}

function renderAmazonButton() {
  const {options, ledgerCurrency} = componentData;
  const configuration = {
    merchantId: 'DIGITALRIVER',
    publicKeyId: 'DIGITALRIVER',
    ledgerCurrency: ledgerCurrency,
    // customize the buyer experience
    productType: 'PayAndShip',
    placement: 'Cart',
    buttonColor: options?.style?.color || 'Gold'
  };
  const checkoutLanguage = getCheckoutLanguage();
  if(checkoutLanguage) {
    configuration.checkoutLanguage = checkoutLanguage;
  }
  const startButton = getElement(START_BUTTON_ID);
  startButton.innerHTML = '';
  const amazonPayButton = window.amazon.Pay.renderButton(`#${START_BUTTON_ID}`, configuration);
  componentData.amazonPayButton = amazonPayButton;
  amazonPayButton.onClick(() => onPaymentEvent(componentData));
}

/**
 * Returns element by ID
 * @param id
 * @returns {HTMLElement}
 */
export function getElement(id) {
  return document.getElementById(id);
}

/**
 * Gets source and generates final button
 * @returns {*}
 */
export function onPaymentEvent(componentData) {
  const paymentServiceSourceData = componentData.options.sourceData;
  return createSource(paymentServiceSourceData).then(function (result) {
    if (result.error) {
      sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'source', result);
      return '';
    } else {
      activateAmazon(result.source);
      sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'source', result);
    }

  });
}


export function getAmazonRegionalData(componentData) {

  const {controller, options} = componentData;
  const {sourceData} = options;
  return getPaymentMethods(controller.id, sourceData.sessionId).then(paymentMethodResponse => {
    const {currency} = paymentMethodResponse.sessionInformation;
    const country = paymentMethodResponse.sessionInformation.country ? paymentMethodResponse.sessionInformation.country : options.sourceData.country;

    return controllerEmitter.send('getAmazonRegion', {
      country,
      currency
    }).then((response) => {
      const region = response.data?.amazonPay?.region;
      return {
        localizedScript: region ? AMAZON_SCRIPT_SRC[region.toLowerCase()] : AMAZON_SCRIPT_SRC.na,
        ledgerCurrency: response.data?.amazonPay?.ledgerCurrency
      };
    });
  });
}
/**
 * Creates source
 * @param paymentServiceSourceData
 * @returns {*}
 */
function createSource(paymentServiceSourceData) {
  return controllerEmitter.send('createSourceFromRequest', {
    componentId: componentData.componentId,
    componentType: componentData.componentType,
    sourceRequest: paymentServiceSourceData,
    type: componentData.componentType
  }).then( res => res.data);
}

/**
 * Sends ready event to client
 */
export function emitComponentReady() {
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'ready');
}

/**
 * Handles creating and clicking final button
 * @param paymentSource
 */
export function activateAmazon(paymentSource) {
  const amazonPayload = getAmazonPayPayload(paymentSource);
  autoClickAmazonPay(amazonPayload);
}

/**
 * Creates Amazon payload needed by final button
 * @param {object} paymentSource
 * @returns {{merchantId, buttonColor: (*|string), createCheckoutSessionConfig: {payloadJSON, signature}, placement: string, publicKeyId, ledgerCurrency, productType, checkoutLanguage: (string|*)}}
 */
function getAmazonPayPayload(paymentSource) {
  const {options} = componentData;
  const {amazonPay} = paymentSource;
  const {merchantId, publicKeyId, signature, payload:payloadJSON, productType, ledgerCurrency} = amazonPay;
  const checkoutLanguage = getCheckoutLanguage();
  const placement = options?.sourceData?.amazonPay?.placement || 'Cart'; //Placement of the Amazon Pay button on your website, Supported values: ‘Home’, ‘Product’, ‘Cart’, ‘Checkout’, and 'Other'
  const buttonColor = options?.style?.color || 'Gold';
  const amazonPayConfig = {
    merchantId,
    publicKeyId,
    ledgerCurrency,
    productType,
    placement,
    buttonColor,
    presentmentCurrency: paymentSource.currency,
    createCheckoutSessionConfig: {
      payloadJSON,
      signature
    }
  };
  if (checkoutLanguage) {
    amazonPayConfig.checkoutLanguage = checkoutLanguage;
  }
  return amazonPayConfig;
}

/**
 * Creates and clicks Amazon pay final button
 * @param payload
 */
function autoClickAmazonPay(payload){
  document.getElementById(START_BUTTON_ID).remove();
  const amazonPayButton = window.amazon.Pay.renderButton(`#${FINAL_BUTTON_ID}`, payload);
  componentData.amazonPayButton = amazonPayButton;
  const finalButton = getElement(FINAL_BUTTON_ID);

  finalButton.click();
}


function getCheckoutLanguage() {
  const optionsCheckoutLanguage = componentData.options?.sourceData?.amazonPay?.checkoutLanguage;
  if(optionsCheckoutLanguage && AMAZON_CHECKOUT_LANGUAGES.includes(optionsCheckoutLanguage)) {
    return optionsCheckoutLanguage;
  } else {
    return undefined;
  }

}