import {getSellingEntity} from '../../client/dropin/terms';

export async function handleLegalFooter(createElement, paymentMethodResponse, locale, accordion, configuration, legalFooterComponent) {
  legalFooterComponent = legalFooterComponent ? legalFooterComponent : {};
  const entity = getSellingEntity(paymentMethodResponse.sessionInformation, paymentMethodResponse.sessionInformation?.country);

  const complianceOptions = {
    compliance: {
      country: paymentMethodResponse.sessionInformation?.country ? paymentMethodResponse.sessionInformation?.country : configuration?.billingAddress?.address?.country,
      language: locale.split('-')[0],
      businessEntityCode: paymentMethodResponse.sessionInformation?.sellingEntityId,
      entity: entity
    }
  };

  if(configuration?.compliance?.jpCommerceLawPageUrl) {
    complianceOptions.jpCommerceLawPageUrl = configuration.compliance.jpCommerceLawPageUrl;
  }

  if(!document.getElementById('DR-Legal-Footer')) {
    const legalFooter = document.createElement('div');
    legalFooter.id = 'DR-Legal-Footer';
    accordion.appendChild(legalFooter);
  }


  if(!legalFooterComponent?.component) {
    const complianceFooter = createElement('compliance', complianceOptions);
    await complianceFooter.mount('DR-Legal-Footer');
    legalFooterComponent.component = complianceFooter;
  } else {
    legalFooterComponent.component.update(complianceOptions);
    accordion.append(document.getElementById('DR-Legal-Footer'));
  }

  return legalFooterComponent.component;

}

