import {getAgreeToTerms, getAutoRenewPlanTerms, getConfirmDisclosure} from '../complianceData';
import {isStoredPaymentMethod} from './stored-payment-methods';
import {countryHasEntity, getEntityForCountry} from '../../app/components/controller/controller-tax-identifer';
import {getStoredId, shouldSkipShowingPaymentMethods} from '../create-dropin';

export function displayShowTermsOfSaleDisclosure(configuration, sessionInformation) {
  return typeof configuration.options !== 'undefined' && (configuration.options?.usage === 'subscription' || sessionInformation !== 'undefined') && configuration.options?.showTermsOfSaleDisclosure;
}

export function isTermsOfSaleDisclosureMode(configuration, sessionInformation) {
  return displayShowTermsOfSaleDisclosure(configuration, sessionInformation) ? configuration.options.showTermsOfSaleDisclosure === true : false;
}

export function getCountry(sessionInformation, configuration) {
  return typeof sessionInformation !== 'undefined' ? sessionInformation.country : configuration.billingAddress.address.country;
}

export function getSellingEntity(sessionInformation, country) {
  return typeof sessionInformation !== 'undefined' ? sessionInformation.sellingEntityId : countryHasEntity(country) ?  getEntityForCountry(country)[0] : 'DR_IRELAND-ENTITY';
}

export function createTermsSourceObject(locale, sessionInformation, configuration) {
  const country = getCountry(sessionInformation, configuration);
  const sellingEntity = getSellingEntity(sessionInformation, country);
  if (sessionInformation?.recurring === true || configuration?.options?.usage === 'subscription') {
    return {
      terms: getTermsOfSaleDisclosureValue(locale, sessionInformation, configuration)
    };
  } else {
    return {
      terms: getConfirmDisclosure(locale, sellingEntity, undefined, country)
    };
  }
}

export function getTermsOfSaleDisclosureValue(locale, sessionInformation, configuration) {
  let country = getCountry(sessionInformation, configuration);
  let sellingEntity = getSellingEntity(sessionInformation, country);
  if ((typeof sessionInformation !== 'undefined' && sessionInformation.recurring === true) || configuration?.options?.usage === 'subscription') {
    return getAutoRenewPlanTerms(locale, sellingEntity, configuration, country);
  } else {
    return getConfirmDisclosure(locale, sellingEntity, configuration, country);
  }
}

export function getAgreeToTermsValue(locale, sellingEntityId) {
  return getAgreeToTerms(locale, sellingEntityId);
}

export function createTermsOfSale(paymentMethodType, locale, sessionInformation, paymentMethod, componentHolder, availablePaymentMethod, configuration) {
  if (sessionInformation?.recurring || configuration?.options?.usage === 'subscription') {
    return createTermsOfSaleRecurring(paymentMethodType, locale, sessionInformation, paymentMethod, componentHolder, availablePaymentMethod, configuration);
  } else {
    return createTermsOfSaleNotRecurring(paymentMethodType, locale, sessionInformation, paymentMethod, componentHolder, availablePaymentMethod, configuration);
  }
}

export function addChangeEventToCheckbox(termsCheckbox, paymentMethod, componentHolder) {
  if (paymentMethod.standaloneButton) {
    termsCheckbox.onchange = function (e) {
      if (e.target.checked) {
        componentHolder.classList.remove('DR-prevent-click');
      } else {
        componentHolder.classList.add('DR-prevent-click');
      }
    };
  }
}

export function determineTermsTextId(paymentMethod) {
  if (isStoredPaymentMethod(paymentMethod)) {
    return 'DR-terms-text'+getStoredId(paymentMethod);
  }
  return 'DR-terms-text-'+paymentMethod.type;
}

export function determineTermsCheckboxErrorId(paymentMethod) {
  if (isStoredPaymentMethod(paymentMethod)) {
    return 'DR-terms-checkbox-error'+getStoredId(paymentMethod);
  }
  return 'DR-terms-checkbox-error-'+paymentMethod.type;
}

export function determineTermsCheckboxId(paymentMethod) {
  if (isStoredPaymentMethod(paymentMethod)) {
    return 'DR-terms-checkbox'+getStoredId(paymentMethod);
  }
  return 'DR-terms-checkbox-'+paymentMethod.type;
}

export function createTermsOfSaleNotRecurring(paymentMethodType, locale, sessionInformation, paymentMethod, componentHolder, availablePaymentMethod, configuration) {
  const termsParent = document.createElement('div');
  const termsRow = document.createElement('div');
  const termsText = document.createElement('div');
  const termsLabel = document.createElement('label');
  const termsCheckboxParent = document.createElement('div');
  const termsCheckbox = document.createElement('input');
  const termsError = document.createElement('div');

  termsText.className = 'DR-terms-content';
  termsLabel.innerHTML = getTermsOfSaleDisclosureValue(locale, sessionInformation, configuration);
  termsCheckbox.setAttribute('aria-required', 'true');
  termsCheckbox.type = 'checkbox';
  termsError.className = 'DR-terms-checkbox-error DR-error-message';
  termsError.setAttribute('aria-live', 'assertive');

  if(shouldSkipShowingPaymentMethods(sessionInformation, configuration)) {
    termsParent.className = 'DR-terms DR-terms-skipPaymentMethods';
    termsError.id = 'DR-terms-checkbox-error-skipPaymentMethods';
    termsRow.className = 'DR-terms-row DR-terms-row-skipPaymentMethods';
    termsText.id = 'DR-terms-text-skipPaymentMethods';
    termsCheckbox.id = 'DR-terms-checkbox-skipPaymentMethods';
    termsCheckbox.setAttribute('name', 'DR-terms-checkbox-skipPaymentMethods');
    termsCheckbox.setAttribute('aria-errormessage', 'DR-terms-checkbox-error-skipPaymentMethods');
    termsCheckbox.setAttribute('aria-labelledby', 'DR-terms-text-skipPaymentMethods');
    termsCheckboxParent.className = 'DR-terms-checkbox DR-terms-checkbox-skipPaymentMethods';
  }
  else {
    termsParent.className = 'DR-terms DR-terms-' + paymentMethodType;
    termsError.id = determineTermsCheckboxErrorId(availablePaymentMethod);
    termsRow.className = 'DR-terms-row DR-terms-row-' + paymentMethodType;
    termsText.id = determineTermsTextId(availablePaymentMethod);
    termsCheckbox.id = determineTermsCheckboxId(availablePaymentMethod);
    termsCheckbox.setAttribute('name', determineTermsCheckboxId(availablePaymentMethod));
    termsCheckbox.setAttribute('aria-errormessage', determineTermsCheckboxErrorId(availablePaymentMethod));
    termsCheckbox.setAttribute('aria-labelledby', `${determineTermsTextId(availablePaymentMethod)}-label`);
    termsCheckboxParent.className = 'DR-terms-checkbox DR-terms-checkbox-' + paymentMethodType;
  }
  termsLabel.setAttribute('for', termsCheckbox.getAttribute('name'));
  termsLabel.id = `${determineTermsTextId(availablePaymentMethod)}-label`;
  termsText.appendChild(termsLabel);
  termsCheckboxParent.appendChild(termsCheckbox);
  termsRow.appendChild(termsCheckboxParent);
  termsRow.appendChild(termsText);
  termsParent.appendChild(termsRow);
  termsParent.appendChild(termsError);

  addChangeEventToCheckbox(termsCheckbox, paymentMethod, componentHolder);

  return termsParent;
}


export function createTermsOfSaleRecurring(paymentMethodType, locale, sessionInformation, paymentMethod, componentHolder, availablePaymentMethod, configuration) {
  const termsParent = document.createElement('div');
  termsParent.className = 'DR-terms DR-terms-' + paymentMethodType;

  const termsError = document.createElement('div');
  termsError.id = determineTermsCheckboxErrorId(availablePaymentMethod);
  termsError.className = 'DR-terms-checkbox-error DR-error-message';
  termsError.setAttribute('aria-live', 'assertive');

  const termsRow = document.createElement('div');
  termsRow.className = 'DR-terms-row DR-terms-row-' + paymentMethodType;
  const termsText = document.createElement('div');
  termsText.id = determineTermsTextId(availablePaymentMethod);

  const termsCheckboxParent = document.createElement('div');
  const termsCheckbox = document.createElement('input');
  termsCheckbox.id = determineTermsCheckboxId(availablePaymentMethod);
  termsCheckbox.setAttribute('name', determineTermsCheckboxId(availablePaymentMethod));
  termsCheckbox.setAttribute('aria-required', 'true');
  termsCheckbox.setAttribute('aria-errormessage', determineTermsCheckboxErrorId(availablePaymentMethod));
  termsCheckbox.setAttribute('aria-labelledby', determineTermsTextId(availablePaymentMethod));
  termsCheckbox.type = 'checkbox';

  termsCheckboxParent.className = 'DR-terms-checkbox DR-terms-checkbox-' + paymentMethodType;
  termsCheckboxParent.appendChild(termsCheckbox);

  termsText.className = 'DR-terms-content DR-terms-'+paymentMethodType;

  const termsContentParent = document.createElement('div');
  termsContentParent.className = 'DR-terms-of-sale-disclosure DR-terms-of-sale-disclosure-'+paymentMethodType ;
  termsContentParent.innerHTML = getTermsOfSaleDisclosureValue(locale, sessionInformation, configuration);

  termsText.appendChild(termsContentParent);

  const termsAgreeParent = document.createElement('div');
  termsAgreeParent.className = 'DR-agree-terms-row DR-agree-terms-row-'+paymentMethodType;

  const termsAgreeText = document.createElement('label');
  termsAgreeText.id = determineTermsTextId(availablePaymentMethod);
  termsAgreeText.setAttribute('for', termsCheckbox.getAttribute('name'));
  termsAgreeText.className = 'DR-agree-terms-content DR-agree-terms-content-'+paymentMethodType;
  termsAgreeText.innerHTML = getAgreeToTermsValue(locale, sessionInformation);

  termsAgreeParent.appendChild(termsCheckboxParent);
  termsAgreeParent.appendChild(termsAgreeText);
  termsText.appendChild(termsAgreeParent);
  termsRow.appendChild(termsText);

  termsParent.appendChild(termsRow);
  termsParent.appendChild(termsError);

  addChangeEventToCheckbox(termsCheckbox, paymentMethod, componentHolder);

  return termsParent;
}


export function isTermsDisplayed(paymentMethod) {
  return document.querySelectorAll('.DR-agree-terms-content-'+paymentMethod.type).length > 0;
}

export function getTermsCheckboxDOMElement(paymentMethod) {
  return document.getElementById('DR-terms-checkbox-' + paymentMethod.type);
}