import {appendWallet} from './Wallet';
import {waitForRequiredData} from '../utils';
import {getPaymentMethods} from '../../fetch-payment-methods';
import {attachShippingChoiceToDropinCheckout, getDropinCheckout} from '../checkout-sessions-api';
import {
  createAttachShippingAddressToCheckoutSession,
  createAttachShippingChoiceToCheckoutSession,
  createProcessSourceData
} from './wallet-events';


export function wallet(key, configuration, storage, createElement) {

  let nodeToKeep;

  const mountedComponents = [];

  storage.elements = [];

  const instanceDetails = {

  };

  const callApiAndMount = async function (checkoutData, error) {

    const paymentSessionId = checkoutData?.payment?.session?.id;

    if (checkoutData.requiresShipping && !checkoutData.shippingChoice && checkoutData.options?.shippingMethods) {
      const shippingChoice = {
        'shippingChoice': checkoutData.options.shippingMethods[0]
      };

      await attachShippingChoiceToDropinCheckout(shippingChoice, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);

      const checkout = await getDropinCheckout(configuration.checkoutSessionId, storage.apiKey, storage.fingerprint).then(data => data.json());
      callApiAndMount(checkout);

      return;
    }

    return getPaymentMethods(storage.controller.id, paymentSessionId, null, null, storage.locale, null, null).then((paymentMethodResponse) => {

      instanceDetails.paymentSessionId  = paymentSessionId;
      instanceDetails.checkoutData  = checkoutData;
      instanceDetails.sessionInformation  = paymentMethodResponse.sessionInformation;
      instanceDetails.locale = storage.locale;

      appendWallet(nodeToKeep, {
        ...instanceDetails,
        translations: storage.translations,
        controllerId: storage.controller.id,
        createElement: createElement,
        paymentMethods: paymentMethodResponse.paymentMethods,
        sessionInformation: paymentMethodResponse.sessionInformation,
        error: error,
        attachShippingAddressToCheckoutSession: createAttachShippingAddressToCheckoutSession(configuration, storage),
        attachShippingChoiceToCheckoutSession: createAttachShippingChoiceToCheckoutSession(checkoutData, configuration, storage),
        addElementToStorage: function (element) {
          mountedComponents.push(element);
        },
        getMountedElements: function () {
          return mountedComponents;
        },
        processSourceData: createProcessSourceData(storage, callApiAndMount, configuration)
      });
    });


  };



  return {
    mount: (node) => {

      if (typeof node === 'string') {
        node = document.getElementById(node);
      }

      if (!(node instanceof HTMLElement)) {
        throw new Error('mount() requires a valid HTMLElement or id attribute.');
      }

      nodeToKeep = node;

      waitForRequiredData(storage).then( () => {
        callApiAndMount(storage.checkoutData);
      });


    }

  };

}