import {paymentServiceGetRequest} from '../../payment-service-request';
import {config} from '../config';
import {
  chooseCreateSourceCatchMessage,
  formatComponentTriggerErrors,
  handleCreateSourceValidation,
  runCreateSourceAndHandleResponse
} from './controller-create-source-utils';

export function runGetKonbiniStoresHandleResponse(apiKey) {
  return paymentServiceGetRequest(apiKey, `${config.paymentServiceBaseUrl}/konbini/stores`)
    .then((response) => {
      return response.data;
    })
    .catch(() => {
      return [
      ];
    });
}

function isKobiniKeyNotProvided(sourceRequest) {
  return typeof sourceRequest.konbini === 'undefined';
}

export function handleKonbiniCreateSource(componentInstance, sourceRequest, apiKey) {
  if (!componentInstance) {
    return Promise.reject('Cannot create source because all the required components have not been created for a konbini submission');
  }

  if (!componentInstance.send) {
    return Promise.reject('Cannot create source because all the required components have not been mounted for a konbini submission');
  }

  return componentInstance.send('getComponentData', {}).then((konbiniResponse) => {

    return componentInstance.send('componentTriggerEvent', {type: 'onKonbiniCreateSource'}).then((konbiniData) => {

      let errors = formatComponentTriggerErrors([konbiniData]);

      if (errors.length > 0) {
        return handleCreateSourceValidation(errors);
      }

      if (isKobiniKeyNotProvided(sourceRequest)) {
        sourceRequest.konbini = {};
      }

      sourceRequest.konbini.storeId = konbiniResponse.data;

      return runCreateSourceAndHandleResponse(sourceRequest, apiKey);

    });

  }).catch((error) => {
    return chooseCreateSourceCatchMessage(error);
  });
}