/** functions to determine if data exists and should be rendered **/

/**
 *
 * @param countrySpec
 * @returns {boolean|*}
 */
export function canRenderInvoiceAttribute(countrySpec) {
  if(!countrySpec.invoiceAttributesInfo) {
    return false;
  }
  const hasIndividual = invoiceAttributeTypeFound(countrySpec.invoiceAttributesInfo, 'individual');
  const hasBusiness = invoiceAttributeTypeFound(countrySpec.invoiceAttributesInfo, 'business');
  return countrySpecProvided(countrySpec) && (hasBusiness || hasIndividual);
}

/**
 *
 * @param invoiceAttributeInfo
 * @param type
 * @returns {boolean}
 */
export function invoiceAttributeTypeFound(invoiceAttributeInfo, type) {
  return typeof invoiceAttributeInfo[type] !== 'undefined' && invoiceAttributeInfo[type].length > 0;
}

/**
 *
 * @param countrySpec
 * @returns {boolean}
 */
export function countrySpecProvided(countrySpec) {
  return typeof countrySpec !== 'undefined';
}

/**
 *
 * @param type
 * @returns {boolean}
 */
export function isTypeIndividual(type) {
  return (
    type === 'individual'
  );
}

/**
 *
 * @param type
 * @returns {boolean}
 */
export function isTypeBusiness(type) {
  return (
    type === 'business'
  );
}

/**
 *
 * @param country
 * @returns {boolean}
 */
export function countryIsTaiwan(country) {
  return country === 'TW';
}

/**
 *
 * @param componentId
 * @returns {boolean}
 */
export function isTaiwanAgreementValid(componentId) {
  return document.getElementById(`${componentId}-Taiwan-agreement-checkbox`).checked;
}

/**
 *
 * @param field
 * @returns {string}
 */
export function getFieldKey(field) {
  return field.name + '-' + field.type;
}

/**
 *
 * @param fields
 * @param fieldDetails
 * @returns {array}
 */
export function addFieldDetailsToState(fields, fieldDetails) {
  fields.push(fieldDetails);
  return fields;
}

/**
 *
 * @param selectedShopperType
 * @returns {string}
 */
export function getSelectedInvoiceTypeForShopperType(selectedShopperType) {
  return document.querySelector(`[data-invoice-invoice-type=${selectedShopperType}]`).value;
}

/**
 *
 * @param types
 * @returns {*}
 */
export function formatTypesForReadyEvent(types) {
  if(!types) return;
  return types.map(type => {
    return {
      type: type.type,
      attributes: type.attributes.map(a => ({attributeName: a.attributeName, isRequired: a.isRequired}))
    };
  });
}

/**
 *
 * @param field
 * @returns {string}
 */
export function getDynamicInputType(field) {
  return typeof field.correspondingAttribute !== 'undefined' ? 'dynamicselect' : 'dynamicfield';
}