import {
  createComponent,
  destroy,
  generateComponentId,
  getComponentWindow,
  onEventHandler,
  registerComponentWithController, triggerEvent,
  unmount
} from '../createComponent';
import {
  canRenderInvoiceAttribute,
  invoiceAttributeTypeFound,
  countrySpecProvided,
  isTypeIndividual,
  isTypeBusiness,
  countryIsTaiwan,
  isTaiwanAgreementValid,
  getFieldKey,
  addFieldDetailsToState,
  getSelectedInvoiceTypeForShopperType,
  formatTypesForReadyEvent,
  getDynamicInputType
} from './invoiceAttribute-utils';

import {sanitizeOptionsForType} from '../../app/components/options';
import dataStore from '../dataStore';
import postRobot from '../../post-robot-wrapper';
import {config} from '../../app/components/config';
import {sendEventData} from '../../app/components/input-events';
import {getLocalizedStrings} from '../tax-identifier/tax-identifier-data';
import {setElementErrorMessage} from '../../utils';
import {getButtonTextByLocale} from '../dropin/button-text';
import customSelect from 'custom-select';

let elements = {};
let fields = [];
let selectedInvoiceType;
let selectedShopperType = 'individual';
let componentData = {};
let controllerEmitter;
let controllerListener;

const COMPONENT_TYPE = 'invoiceattribute';
const CUSTOMER_TYPE_INDIVIDUAL = 'individual';
const CUSTOMER_TYPE_BUSINESS = 'business';

export async function componentTriggerEvent(event) {
  const {type} = event.data;
  if (type === 'submit') {
    const taiwanAgreementCheckValid = isTaiwanAgreementCheckValid();
    if (!taiwanAgreementCheckValid) {
      return new Promise(resolve => resolve({
        error: {
          code: 'NEEDS_AGREEMENT'
        }
      }));
    }
    const resolveData = await handleSubmit();
    return new Promise(resolve => resolve(resolveData));
  } else {
    return Promise.resolve();
  }
}

/**
 *
 * @param controllerId
 * @param key
 * @param options
 * @returns {{unmount: unmount, controllerId: (*|null), options: (*|*), destroy: destroy, update: update, id: string, parentNode: null, type: string, mount: mountElement, key: *, on: onEventHandler}}
 */
export function createInvoiceAttribute(controllerId, key, options) {
  if(!options || !options.invoiceAttribute) {
    throw new Error('You must provide an options object with an invoiceAttribute object for this element');
  }
  if(!options.invoiceAttribute.sessionId && (!options.invoiceAttribute.country || !options.invoiceAttribute.sellingEntity)) {
    throw new Error('You must provide either sessionId or country and sellingEntity in the invoiceAttribute object in options');
  }
  const id = generateComponentId(COMPONENT_TYPE);

  const invoiceAttribute = {
    id: id,
    key: key,
    type: COMPONENT_TYPE,
    parentNode: null,
    controllerId: controllerId,
    mount: mountElement,
    destroy: destroyElement,
    on: onEventHandler,
    options: sanitizeOptionsForType(options, COMPONENT_TYPE),
    unmount: unmountElement,
    //update: updateElement,
    window: Window
  };

  componentData = {
    componentType: COMPONENT_TYPE,
    componentId: id,
    controller: {
      id: controllerId,
      window: getComponentWindow(controllerId)
    },
    options: invoiceAttribute.options,
    key: key,
  };

  controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });
  controllerListener = postRobot.listener({window: componentData.controller.window, domain: config.domain});

  controllerListener.on('componentTriggerEvent' + id, componentTriggerEvent);

  invoiceAttribute.submit =  triggerEvent(controllerId, id, 'submit');

  return invoiceAttribute;
}




export function handleOptions(data) {
  const locale = data.instanceOptions.locale;
  const localizedStrings = getLocalizedStrings(locale);
  const parentNode = componentData.parentNode;
  const componentId = componentData.componentId;

  componentData.instanceOptions = data.instanceOptions;
  componentData.options = data.options;
  componentData.countrySpec = JSON.parse(JSON.stringify(data.countrySpec[0])); //we need a deep copy
  componentData.customerTypeFilter = getCustomerType(data);
  componentData.needsTaiwanAgreement = countryIsTaiwan(componentData.countrySpec.country);

  if(countrySpecProvided(componentData.countrySpec?.invoiceAttributesInfo) && componentData.customerTypeFilter) {
    if (isTypeBusiness(componentData.customerTypeFilter)) {
      selectedShopperType = 'business';
      delete componentData.countrySpec.invoiceAttributesInfo.individual;
    } else {
      selectedShopperType = 'individual';
      delete componentData.countrySpec.invoiceAttributesInfo.business;
    }
  }

  if (!canRenderInvoiceAttribute(componentData.countrySpec)) {
    emitComponentReady();
    return;
  }

  addContainerToDom(parentNode, componentId);

  addHeader(componentId, localizedStrings);

  const shouldDisplayRadioButton = displayRadioButtons(componentData.countrySpec);
  if (shouldDisplayRadioButton) {
    addRadioButtons(componentId, localizedStrings);
  }

  const {invoiceAttributesInfo} = componentData.countrySpec;
  const hasIndividual = invoiceAttributeTypeFound(invoiceAttributesInfo, 'individual');
  const hasBusiness = invoiceAttributeTypeFound(invoiceAttributesInfo, 'business');

  if (hasIndividual) {
    createPurchaseContainerForType(componentId, CUSTOMER_TYPE_INDIVIDUAL, invoiceAttributesInfo, locale, localizedStrings);
  }

  if (hasBusiness) {
    createPurchaseContainerForType(componentId, CUSTOMER_TYPE_BUSINESS, invoiceAttributesInfo, locale, localizedStrings);
  }
  if(componentData.needsTaiwanAgreement) {
    const taiwanAgreement = createTaiwanAgreement(componentId, localizedStrings);
    getContainerElement().appendChild(taiwanAgreement);
  }
  if (shouldDisplayRadioButton) {
    document.getElementById(componentId + 'individualPurchaseRadioButton').click();
  }

  const controllerId = componentData.controller.id;
  elements = createElements(controllerId, componentData.key, data.options);
  if (typeof data.options.hideSubmitButton === 'undefined' || data.options.hideSubmitButton === false ) {
    const submitButton = createSubmitButton(componentData);
    document.getElementById(componentData.componentId).appendChild(submitButton);
  }
}

export function createElements(controllerId, key, options) {
  fields.forEach(field => {
    const inputType = getDynamicInputType(field);
    const defaultOptions = {
      placeholderText: '', //this would be where we want first option to go for dynamic select if we decide we need one
      defaultValue: getDefaultValueForField(field.name),
      elementType: field.name,
      componentType: field.name,
      validationPattern: field.pattern,
      labelText: field.localeDataForSchema.displayName,
      helpText: field.localeDataForSchema.helpText,
      type: field.type,
      items: field.items
    };
    const accessibilityName = `invoiceAttribute-${field.name}`;

    if (typeof options.classes !== 'undefined') {
      defaultOptions.classes = options.classes;
    }

    if (typeof options.style !== 'undefined') {
      defaultOptions.style = options.style;
    }

    const fieldKey = getFieldKey(field);

    elements[fieldKey] = {
      parentId: fieldKey,
      elementType: field.name,
      options: defaultOptions,
      component: createComponent(inputType, controllerId, key, defaultOptions, accessibilityName),
      correspondingAttribute: field.correspondingAttribute,
      items: field.items
    };

    if(getDefaultValueForField(field.name)) {
      elements[fieldKey].complete = true;
      elements[fieldKey].value = getDefaultValueForField(field.name);
    }

  });

  const data = dataStore.get(key);
  Object.keys(elements).forEach(function (key) {
    const componentData = elements[key];
    data.components = Object.assign({}, data.components, { [componentData.component.id]: componentData.component.id });
    const component = componentData.component;

    registerComponentWithController(controllerId, component, componentData.options);

    component.on('ready', () => {handleFieldReady(componentData.parentId, elements);});
    component.on('change', (details) => {handleFieldChange(componentData.parentId, details);});
  });

  return elements;
}

export function mountElement(parentNodeId) {
  if (this.parentNode !== null) {
    throw new Error(`Can not mount, only one ${this.type} can be mounted at a time.`);
  }

  let parent = typeof parentNodeId === 'string' ? document.getElementById(parentNodeId) : parentNodeId;

  if (!(parent instanceof HTMLElement)) {
    throw new Error('mount() requires a valid HTMLElement or id attribute.');
  }

  componentData.window = window;
  const data = dataStore.get(this.key);

  data.components[this.type] = { 'parent': parent, 'options': sanitizeOptionsForType(this.options, this.type) };
  dataStore.set(this.key, data);

  this.parentNode = parent;

  const componentId = this.id;

  componentData.parentNode = parent;

  return controllerEmitter.send('mountClientComponent', {
    componentId: componentData.componentId,
    componentType: componentData.componentType
  }).then((response) => {
    handleOptions(response.data);
    mountElements(componentId, elements);
  });

}

function mountElements(parentNode, components) {
  if(Object.keys(elements).length > 0) {
    Object.keys(components).forEach(function (key) {
      const componentData = components[key];
      const elementDivId = componentData.parentId + '-' +parentNode;
      componentData.component.mount(elementDivId);
    });
    selectedInvoiceType = getSelectedInvoiceTypeForShopperType(selectedShopperType);
  }

}

export function unmountElement() {
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.unmount();
  });
  unmount.call(this);
}

function destroySubElements() {
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.destroy();
  });
}

function resetData() {
  delete componentData.countrySpec;
  elements = {};
  fields = [];
}

function destroyElement() {
  destroySubElements();
  resetData();
  return destroy.call(this);
}

function updateElementsState(elements, element, details) {
  elements[element] = Object.assign({},elements[element], details);
}

function handleInvoiceTypeChange(event) {
  selectedShopperType = event.target.getAttribute('data-invoice-invoice-type');
  selectedInvoiceType = event.target.value;
  toggleSubmitButton(componentData.componentId);
  const invoiceTypeContainers = document.querySelectorAll(`[data-invoice-shopper-type=${selectedShopperType}]`);
  invoiceTypeContainers.forEach(container => {
    const containerInvoiceType = container.getAttribute('data-invoice-type');
    container.style.display = containerInvoiceType === selectedInvoiceType ? 'block' : 'none';
  });
}

export function handleFieldChange(elementType, details) {
  updateElementsState(elements, elementType, details);
  handleOnChangeEvent(elements, elementType, details, componentData);
}

export function handleFieldReady(elementType, elements) {
  updateElementsState(elements, elementType, {ready: true});
  handleReady(elements);
  // if dynamic select doesn't have placeholder, we are preselecting a value
  if(elements[elementType].component.type === 'dynamicselect' && !elements[elementType].component.placeholderText) {
    elements[elementType].complete = true;
    elements[elementType].value = elements[elementType].items[0].value;
  }
}

function handleOnChangeEvent(elements, elementType, eventData, componentData) {
  const {componentId} = componentData;
  toggleSubmitButton(componentId);
  resetErrorMessages(componentId, elements);
  if(eventData.error) {
    setErrorMessages(componentId, elements);
  }

}


function handleReady(elements) {
  const elementsNotReady = Object.keys(elements).filter( key =>elements[key].ready !== true);
  if(elementsNotReady.length === 0) {
    emitComponentReady();
  }
}

function getInvoiceAttributeData() {
  let invoiceAttributes = {
    hasInvoices: canRenderInvoiceAttribute(componentData.countrySpec),
  };

  if(canRenderInvoiceAttribute(componentData.countrySpec)) {
    if (componentData.customerTypeFilter && typeof componentData.countrySpec?.invoiceRules?.[componentData.customerTypeFilter] !== 'undefined') {
      invoiceAttributes.invoiceRequired = componentData.countrySpec.invoiceRules[componentData.customerTypeFilter].invoiceIsMandatory;
    }

    const business = formatTypesForReadyEvent(componentData.countrySpec.invoiceAttributesInfo.business);
    const hasBusinessInvoiceTypes = business && business.length > 0;


    const individual = formatTypesForReadyEvent(componentData.countrySpec.invoiceAttributesInfo.individual);
    const hasIndividualInvoiceTypes = individual && individual.length > 0;

    if(componentData.customerTypeFilter) {
      if(isTypeIndividual(componentData.customerTypeFilter)) {
        invoiceAttributes.individual = individual;
        invoiceAttributes.hasIndividualInvoiceTypes = hasIndividualInvoiceTypes;
        selectedShopperType = 'individual';
      }
      if(isTypeBusiness(componentData.customerTypeFilter)) {
        invoiceAttributes.business = business;
        invoiceAttributes.hasBusinessInvoiceTypes = hasBusinessInvoiceTypes;
        selectedShopperType = 'business';
      }
    } else {
      invoiceAttributes.individual = individual;
      invoiceAttributes.hasIndividualInvoiceTypes = hasIndividualInvoiceTypes;
      invoiceAttributes.business = business;
      invoiceAttributes.hasBusinessInvoiceTypes = hasBusinessInvoiceTypes;
    }
  }
  return invoiceAttributes;
}

export function emitComponentReady() {
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'ready', getInvoiceAttributeData());
}



function getItemsFromAllowedValues(allowedValues, locale, correspondingAttribute) {
  return allowedValues.map(option => ({value: option.value, label: option.locales[locale.replace('-','_')].displayName, [correspondingAttribute]:option[correspondingAttribute]}));
}

function getCustomerType(data) {
  if(data.sessionCustomerType) {
    return data.sessionCustomerType;
  } else {
    return data.options.invoiceAttribute.type || null;
  }
}


function handleSubmit(e) {
  e?.preventDefault();
  const fields = getFieldsForInvoiceType();
  const attributes = {};
  fields.forEach(field => {
    attributes[field.attributeName] = elements[`${field.attributeName}-${selectedInvoiceType}-${selectedShopperType}`].value;
    if(field.correspondingAttribute) {
      attributes[field.correspondingAttribute] = getCorrespondingAttributeValue(field, field.correspondingAttribute, attributes[field.attributeName]);
    }
  });
  return controllerEmitter.send('submitInvoice', {
    type: selectedInvoiceType,
    attributes: attributes
  }).then((response) => {
    const eventData = response.data;
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'complete', eventData);
    return eventData;
  });
}

function getCorrespondingAttributeValue(field, correspondingAttribute, attributeValue) {
  return field.allowedValues.find(a => a.value === attributeValue)[correspondingAttribute];
}
function getRequiredFieldsForSelectedInvoiceType() {
  let invoiceFields = getFieldsForInvoiceType();
  return invoiceFields ? invoiceFields.filter(a => a.isRequired) : [];
}

function getFieldsForInvoiceType() {
  return componentData.countrySpec.invoiceAttributesInfo[selectedShopperType].filter(a => a.type === selectedInvoiceType).map(b => b.attributes)[0];
}

function getDefaultValueForField(fieldName) {
  const defaults = {
    MEMBER_CARRIER: componentData.options.invoiceAttribute.email || ''
  };
  return defaults[fieldName];
}

function requiredFieldsAreValid(fields) {
  return fields.every(field => {
    const element = getElement(field.attributeName, selectedInvoiceType, selectedShopperType);
    return element.complete;
  });
}

function getElement(fieldType, invoiceType, shopperType) {
  return elements[`${fieldType}-${invoiceType}-${shopperType}`];
}

// dom/layout functions
export function addContainerToDom(parentNode, componentId) {
  parentNode.innerHTML += `<div id="${componentId}" class="DR-invoice-attribute-container"></div>`;
}

function getContainerElement() {
  return document.getElementById(componentData.componentId);
}

function addHeader(uniqueId, localizedStrings) {
  const header = document.createElement('div');
  header.id = uniqueId+'header';
  header.innerText = localizedStrings.invoice;
  header.className = 'DR-title';
  getContainerElement().appendChild(header);
}

export function createPurchaseContainerForType(componentId, type, invoiceAttributesInfo, locale, localizedStrings) {
  const {options} = componentData;
  const normalizedLocale = locale.replace('-', '_');
  const purchaseContainer = document.createElement('div');
  purchaseContainer.id = `${componentId}${type}PurchaseContainer`;

  const typeSelectLabel = document.createElement('label');
  typeSelectLabel.setAttribute('for', `${componentId}-${type}-invoice-type`);
  typeSelectLabel.innerText = localizedStrings.invoiceCarrier;
  purchaseContainer.appendChild(typeSelectLabel);
  const typeSelect = createTypeSelect(type, invoiceAttributesInfo, normalizedLocale);

  purchaseContainer.appendChild(typeSelect);

  invoiceAttributesInfo[type].forEach(invoiceAttribute => {

    const invoiceAttributeFieldContainer = document.createElement('div');
    invoiceAttributeFieldContainer.id = `${componentId}-${type}-${invoiceAttribute.type}-container`;

    if(typeSelect.value !== invoiceAttribute.type) {
      invoiceAttributeFieldContainer.style.display = 'none';
    }

    invoiceAttributeFieldContainer.setAttribute('data-invoice-shopper-type', type);
    invoiceAttributeFieldContainer.setAttribute('data-invoice-type', invoiceAttribute.type);
    purchaseContainer.append(invoiceAttributeFieldContainer);

    invoiceAttribute.attributes.forEach(attribute => {
      const formattedLocale = locale.replace('-','_');
      const localeDataForSchema = attribute.locales[formattedLocale];
      if(localeDataForSchema) {
        const items = attribute.allowedValues ? getItemsFromAllowedValues(attribute.allowedValues, formattedLocale, attribute.correspondingAttribute) : null;

        const fieldDetails = {
          name: attribute.attributeName,
          pattern: attribute.formatPattern,
          maxLength: attribute.maxLength,
          minLength: attribute.minLength,
          localeDataForSchema: localeDataForSchema,
          type: `${invoiceAttribute.type}-${type}`,
          correspondingAttribute: attribute.correspondingAttribute,
          items: items,
          isRequired: attribute.isRequired
        };

        addFieldToDOM(type, invoiceAttribute.type, invoiceAttributeFieldContainer, attribute.attributeName, localeDataForSchema, componentId);
        fields = addFieldDetailsToState(fields, fieldDetails);
      } else {
        throw new Error(`Locale ${locale} is not supported for this country and sellingEntity`);
      }


    });
  });
  getContainerElement().appendChild(purchaseContainer);
  customizeSelects(purchaseContainer.id, options);
}

function createTypeSelect(type, invoiceAttributesInfo, normalizedLocale) {

  const {componentId} = componentData;
  const typeSelect = document.createElement('select');
  typeSelect.id = `${componentId}-${type}-invoice-type`;
  typeSelect.name = `${componentId}-${type}-invoice-type`;
  typeSelect.setAttribute('data-invoice-invoice-type', type);
  typeSelect.addEventListener('change', handleInvoiceTypeChange);

  invoiceAttributesInfo[type].forEach(invoiceAttribute => {
    const invoiceAttributeOption = document.createElement('option');
    invoiceAttributeOption.id = `${componentId}-${invoiceAttribute.type}-invoice-option`;
    invoiceAttributeOption.value = invoiceAttribute.type;
    invoiceAttributeOption.innerText = invoiceAttribute.locales && invoiceAttribute.locales[normalizedLocale] ? invoiceAttribute.locales[normalizedLocale].displayName : invoiceAttribute.type;
    typeSelect.appendChild(invoiceAttributeOption);
  });

  return typeSelect;
}

function customizeSelects(purchaseContainerId, options) {
  const customSelects = customSelect(`#${purchaseContainerId} select`);
  customSelects.forEach(select => {
    select.container.classList.add(options.classes ? options.classes.complete : 'DRElement--complete');
    select.container.classList.add('DR-row');
  });
}

function addFieldToDOM(customerType, invoiceType, container, fieldType, localeData, componentId) {
  const row = document.createElement('div');
  row.className = 'DR-row DR-invoice-attribute-field';
  // hiding fields where data has been passed in from options
  row.setAttribute('data-field-type', fieldType);
  if(getDefaultValueForField(fieldType)) {
    row.style.display = 'none';
  }

  const dataParent = document.createElement('div');
  dataParent.className = 'DR-fieldGroup';

  const nameField = document.createElement('label');
  nameField.innerHTML = localeData.displayName;
  nameField.className = 'DR-field-label';
  nameField.setAttribute('aria-hidden', 'true');

  const inputFieldParent = document.createElement('div');
  inputFieldParent.id = `${fieldType}-${invoiceType}-${customerType}-${componentId}`;
  inputFieldParent.className = 'DR-field-parent';

  const errorMessage = document.createElement('div');
  errorMessage.className = 'DR-error-message';
  errorMessage.id = `${fieldType}-${invoiceType}-${customerType}-${componentId}-error`;
  errorMessage.setAttribute('aria-hidden', 'true');

  dataParent.appendChild(nameField);
  dataParent.appendChild(inputFieldParent);
  row.appendChild(dataParent);
  row.appendChild(errorMessage);

  container.appendChild(row);

}

function createTaiwanAgreement(componentId, localizedStrings) {
  const twAgreementContainer = document.createElement('div');
  twAgreementContainer.id = `${componentId}-Taiwan-agreement-container`;
  twAgreementContainer.className = 'DR-row';

  const twAgreementCheckbox = document.createElement('input');
  twAgreementCheckbox.id = `${componentId}-Taiwan-agreement-checkbox`;
  twAgreementCheckbox.name = `${componentId}-Taiwan-agreement-checkbox`;
  twAgreementCheckbox.type = 'checkbox';
  twAgreementCheckbox.addEventListener('change', () => {toggleSubmitButton(componentId);});

  const twAgreementLabel = document.createElement('label');
  twAgreementLabel.id = `${componentId}-Taiwan-agreement-label`;
  twAgreementLabel.setAttribute('for', `${componentId}-Taiwan-agreement-checkbox`);
  twAgreementLabel.innerText = localizedStrings.taiwanInvoiceTermsAndConditions;


  twAgreementContainer.appendChild(twAgreementCheckbox);
  twAgreementContainer.appendChild(twAgreementLabel);
  return twAgreementContainer;
}

function addRadioButtons(uniqueId, localizedStrings) {

  const purchaseTypeElement = document.createElement('div');
  purchaseTypeElement.innerHTML = localizedStrings.purchaseType;
  purchaseTypeElement.className = 'DR-shopper-type';
  purchaseTypeElement.id = 'DR-shopper-type';

  const radioButtonParent = document.createElement('div');
  radioButtonParent.id = uniqueId+'radioContainer';
  radioButtonParent.className = 'DR-row DR-flex-row DR-standard-padding';
  radioButtonParent.setAttribute('role', 'radiogroup');
  radioButtonParent.setAttribute('aria-labelledby', 'DR-shopper-type');

  const personalPurchaseRadioParent = document.createElement('div');
  personalPurchaseRadioParent.className = 'DR-radioGroup';
  const personalPurchaseRadioButtonLabel = document.createElement('label');
  personalPurchaseRadioButtonLabel.htmlFor = uniqueId + 'individualPurchaseRadioButton';
  personalPurchaseRadioButtonLabel.innerText =  localizedStrings.personalPurchase;
  const personalPurchaseRadioButton = document.createElement('input');
  personalPurchaseRadioButton.checked = true;
  personalPurchaseRadioButton.id = uniqueId + 'individualPurchaseRadioButton';
  personalPurchaseRadioButton.type = 'radio';
  personalPurchaseRadioButton.name = 'shopperType';
  personalPurchaseRadioButton.value = 'individual';
  personalPurchaseRadioButton.addEventListener('click', () => {
    document.getElementById(uniqueId + 'individualPurchaseContainer').style.display = 'block';
    document.getElementById(uniqueId + 'businessPurchaseContainer').style.display = 'none';
    selectedShopperType = 'individual';
    selectedInvoiceType = getSelectedInvoiceTypeForShopperType(selectedShopperType);
    toggleSubmitButton(uniqueId);
  });

  const businessPurchaseRadioParent = document.createElement('div');
  businessPurchaseRadioParent.className = 'DR-radioGroup';
  const businessPurchaseRadioButtonLabel = document.createElement('label');
  businessPurchaseRadioButtonLabel.htmlFor = uniqueId + 'businessPurchaseRadioButton';
  businessPurchaseRadioButtonLabel.innerText = localizedStrings.businessPurchase;
  const businessPurchaseRadioButton = document.createElement('input');
  businessPurchaseRadioButton.id = uniqueId + 'businessPurchaseRadioButton';
  businessPurchaseRadioButton.type = 'radio';
  businessPurchaseRadioButton.name = 'shopperType';
  businessPurchaseRadioButton.value = 'business';
  businessPurchaseRadioButton.addEventListener('click', () => {
    document.getElementById(uniqueId + 'individualPurchaseContainer').style.display = 'none';
    document.getElementById(uniqueId + 'businessPurchaseContainer').style.display = 'block';
    selectedShopperType = 'business';
    selectedInvoiceType = getSelectedInvoiceTypeForShopperType(selectedShopperType);
    toggleSubmitButton(uniqueId);
  });

  radioButtonParent.appendChild(purchaseTypeElement);

  personalPurchaseRadioParent.appendChild(personalPurchaseRadioButton);
  personalPurchaseRadioParent.appendChild(personalPurchaseRadioButtonLabel);

  radioButtonParent.appendChild(personalPurchaseRadioParent);

  businessPurchaseRadioParent.appendChild(businessPurchaseRadioButton);
  businessPurchaseRadioParent.appendChild(businessPurchaseRadioButtonLabel);

  radioButtonParent.appendChild(businessPurchaseRadioParent);

  getContainerElement().appendChild(radioButtonParent);

}

function displayRadioButtons(countrySpec) {
  const type = componentData.options.invoiceAttribute.type;
  const hasIndividual = invoiceAttributeTypeFound(countrySpec.invoiceAttributesInfo, 'individual') && type !== 'business';
  const hasBusiness = invoiceAttributeTypeFound(countrySpec.invoiceAttributesInfo, 'business') && type !== 'individual';
  return hasIndividual && hasBusiness;
}

function isTaiwanAgreementCheckValid() {
  return !componentData.needsTaiwanAgreement ? true : isTaiwanAgreementValid(componentData.componentId);
}

function toggleSubmitButton(componentId) {
  const requiredFields = getRequiredFieldsForSelectedInvoiceType();
  const submitButton = document.getElementById(`${componentId}-submit-button`);
  const taiwanAgreementCheckValid = isTaiwanAgreementCheckValid();
  if(submitButton) {
    submitButton.disabled = !(taiwanAgreementCheckValid && requiredFieldsAreValid(requiredFields));
    if(submitButton.disabled) {
      submitButton.classList.add('DR-button-disabled');
    } else {
      submitButton.classList.remove('DR-button-disabled');
    }
  }
}

function setErrorMessages(uniqueId, elements) {
  Object.keys(elements).forEach(function (element) {
    if (elements[element].error) {
      const errorMessage = elements[element].options.helpText;
      const parentId = elements[element].parentId;
      setElementErrorMessage(`${parentId}-${uniqueId}-error`, errorMessage);
    }
  });
}

function resetErrorMessages(uniqueId, elements) {
  Object.keys(elements).forEach(function (elementType) {
    const parentId = elements[elementType].parentId;
    setElementErrorMessage(`${parentId}-${uniqueId}-error`, '');
  });
}

function createSubmitButton(componentData) {
  const locale = componentData.instanceOptions.locale;
  const submitButton = document.createElement('button');
  submitButton.id = `${componentData.componentId}-submit-button`;
  submitButton.className = 'DR-invoice-attribute-button DR-button-text DR-button-disabled';
  submitButton.type = 'submit';
  submitButton.innerText = getButtonTextByLocale(locale,'submit');
  submitButton.disabled = true;
  submitButton.addEventListener('click', handleSubmit);
  return submitButton;
}