clientListener.on('updateSource', handleUpdateSource);

/**
 * handleUpdateSource handles updating a payment source
 * @param event
 * @returns {object} payment source or errors
 */
export async function handleUpdateSource(event) {
  const {sourceData, cardExpirationElement, locale} = event.data;
  const sourceClientSecret = sourceData.clientSecret.split('_')[1];
  const paymentSourceId = sourceData.id;
  const apiKey = components['controller'].apiKey;
  let expirationDate, expirySplit;

  let updatedSourceData = Object.assign({type: 'creditCard', creditCard: {}}, sourceData);
  delete updatedSourceData.id;
  delete updatedSourceData.clientSecret;

  if (!apiKey) {
    return Promise.reject('Controller did not receive an api key');
  }

  if(cardExpirationElement) {
    const creditCardExpiryComponent = components['cardexpiration'];
    expirationDate = await creditCardExpiryComponent.send('getComponentData', {});
    const expirationDateValidation = validator.validateExpiry(expirationDate.data);
    if(expirationDateValidation.error) {
      return Promise.resolve({
        'error': {
          'type': 'validation_error',
          'errors': [{
            'code': expirationDateValidation.errorType,
            'message': getLocaleMessage(locale, expirationDateValidation.messageCode)
          }]
        },
        'source':null});
    }

    expirySplit = expirationDate.data.split('/');
    updatedSourceData.creditCard = {
      expirationMonth: parseInt(expirySplit[0]),
      expirationYear: parseInt(20 + expirySplit[1]),
    };
  }

  return updateSourceAndHandleResponse(paymentSourceId, sourceClientSecret, apiKey, updatedSourceData);
}

