clientListener.on('handleDropInRedirect', handleDropInRedirect);
componentListener.on('handleDropInRedirect', handleDropInRedirect);

function isSecurityModeEnabled(redirectWindow) {
  return redirectWindow === null;
}

export function handleDropInRedirect(event) {

  const {sourceId, clientSecret, redirectWindow, paymentMethodType, resolve} = event.data;

  delete components['controller'].redirectWindowData;
  components['controller'].sourceId = sourceId;
  components['controller'].clientSecret = clientSecret;
  components['controller'].paymentMethodType = paymentMethodType;

  components['controller'].redirectWindow = redirectWindow;

  if (!isSecurityModeEnabled(redirectWindow)) {
    components['controller'].redirectWindowData = {};
    setRedirectWindowData(components['controller'].redirectWindowData, redirectWindow, determineEvent, paymentMethodType, resolve);
  } else {
    sendCancelEvent(paymentMethodType, resolve);
  }

  return Promise.resolve();
}


function isDRJS(error) {
  return error.message.includes('No handler found for post message: redirectComplete');
}

export function determineEvent(paymentMethodType, resolve) {

  return handleRedirectComplete().then( data => {
    if (data?.source?.state === 'pending_redirect') {
      return sendCancelEvent(paymentMethodType, resolve);
    } else {
      const emitter = paymentMethodType === 'adyen_redirect' ? adyenEmitter : clientEmitter;
      return emitter.send('redirectComplete', {
        response: data,
        action: 'return',
        paymentMethodType: paymentMethodType
      });
    }

  });

}

export function sendCancelEvent(paymentMethodType, resolve) {

  return clientEmitter.send('redirectComplete', {
    action: 'cancel',
    paymentMethodType: paymentMethodType
  }).catch( error => {
    if (isDRJS(error)) { // NOTE: This is the only way to determine is dropin is being used.
      resolve({
        error: {
          type: '3ds1_error',
          errors: [{
            code: '3ds1_canceled',
            message: 'User canceled 3DS1'
          }]
        },
        source: null
      });
    } else {
      return Promise.reject(error);
    }
  });
}

