import {appendTaxExemption} from './TaxExemption';
import { attachTaxCertificateToDropinCheckout, uploadFile } from '../checkout-sessions-api';
import { validateClientDomElement, waitForRequiredData } from '../utils';
import taxAuthority from './tax-authority.json';

export function defaultError() {
  return {
    taxExemption: {},
    main: ''
  };
}

function setErrorMessageAndRemount(storage, callApiAndMount, errorMessage) {
  callApiAndMount(storage.checkoutData, {
    ...defaultError(),
    main: errorMessage
  });
}

function validateRequiredInput(properties, requiredFields, storage, errors) {
  Object.keys(properties).forEach(key => {

    const isRequired = requiredFields.includes(key);
    const value = storage?.[key];

    if (isRequired && (typeof value === 'undefined' || value.length === 0)) {
      errors['taxExemption'][key] = {
        code: 'LENGTH',
        message: ''
      };
    } else {
      delete errors['taxExemption'][key];
    }
  });
}

export function taxExemption(key, configuration, storage) {

  let nodeToKeep;

  storage.taxCertificate = {};

  const taxExemptionSchema = {
    'companyName': {
      'type': 'string',
      'description': 'company name',
      'minLength': 0,
      'maxLength': 79
    },
    'certificateTaxAuthority': {
      'type': 'string',
      'description': 'certificate tax authority',
      'enum': taxAuthority
    },
    'startDate': {
      'type': 'string',
      'description': 'start date',
      'minLength': 0,
      'maxLength': 10
    },
    'expirationDate': {
      'type': 'string',
      'description': 'expiration date',
      'minLength': 0,
      'maxLength': 10
    },
    'taxCertificate': {
      'type': 'string',
      'description': 'tax certificate'
    }
  };
  const taxExemptionRequiredFields = ['companyName', 'certificateTaxAuthority', 'startDate', 'expirationDate', 'taxCertificate'];

  const callApiAndMount = async function (checkoutData, errors) {
    if (!checkoutData.customerId) return;

    appendTaxExemption(nodeToKeep, {
      errors: typeof errors === 'undefined' ? defaultError() : errors,
      translations: storage.translations,
      taxExemptionSchema: taxExemptionSchema,
      taxExemptionRequiredFields: taxExemptionRequiredFields,
      getTaxCertificate: function () {
        return storage.taxCertificate;
      },
      updateTaxCertificate: function (taxCertificate) {
        storage.taxCertificate = taxCertificate;
      },
      updateErrors: function (errors) {
        callApiAndMount(checkoutData, errors);
      },
      updateTaxCertificateAndErrors: function (taxCertificate, errors) {
        storage.taxCertificate = taxCertificate;
        callApiAndMount(checkoutData, errors);
      }
    });
  };

  return {
    mount: (node) => {

      nodeToKeep = validateClientDomElement(node);

      storage.subscribers.push(callApiAndMount);

      waitForRequiredData(storage).then( () => {
        callApiAndMount(storage.checkoutData);
      });

    },
    done : async function () {

      const errors = defaultError();

      setErrorMessageAndRemount(storage, callApiAndMount, '');

      validateRequiredInput(taxExemptionSchema, taxExemptionRequiredFields, storage.taxCertificate, errors);

      // If not valid, then notify UI
      if (Object.keys(errors.taxExemption).length > 0) {
        callApiAndMount(storage.checkoutData, errors['taxExemption']);
        return false;
      }

      const fileToUpload = {
        file: storage.taxCertificate.base64,
        purpose: 'tax_document_customer_upload',
        fileName: storage.taxCertificate.filename,
        title: `US Tax Exemption - ${storage.taxCertificate.filename}`
      };

      try {
        const response = await uploadFile(fileToUpload, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);

        if (response.status !== 201) {
          setErrorMessageAndRemount(storage, callApiAndMount, storage.translations.error.notUploaded);
          return false;
        }

        const uploadedFile = await response.json();
        
        const taxCertificateToAdd = {
          taxCertificate: {
            companyName: storage.taxCertificate.companyName,
            taxAuthority: storage.taxCertificate.certificateTaxAuthority,
            startDate: `${storage.taxCertificate.startDate}T00:00:00Z`,
            endDate: `${storage.taxCertificate.expirationDate}T00:00:00Z`,
            fileId: uploadedFile.id
          }
        };

        const checkoutSession = await attachTaxCertificateToDropinCheckout(taxCertificateToAdd, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);

        if (checkoutSession.status !== 200) {
          setErrorMessageAndRemount(storage, callApiAndMount, storage.translations.error.taxInfoNotSubmitted);
          return false;
        }

        storage.notify();
        return true;

      } catch (e) {
        setErrorMessageAndRemount(storage, callApiAndMount, storage.translations.error.taxInfoNotSubmitted);
        return false;
      }
    }
  };
}