import {clearRedirectData} from '../../../client/dropin-window-data';

export const REDIRECT_STORAGE_ACTION_KEY = 'DRRedirectAction';
export const REDIRECT_STORAGE_SOURCE_AUTHENTICATION_ID = 'DRRedirectSourceAuthenticationId';
export const REDIRECT_STORAGE_SESSION_AUTHENTICATION_ID = 'DRRedirectSessionAuthenticationId';

function redirectWindowDataWasSet(redirectWindowData) {
  return typeof redirectWindowData !== 'undefined';
}

function isRedirectAction(action) {
  return action.includes('return') || action.includes('cancel');
}

export function createHandleStorageEvent(components, clientEmitter, adyenEmitter, handleRedirectComplete) {
  return function (event) {

    if (event.key === REDIRECT_STORAGE_ACTION_KEY && isRedirectAction(event.newValue)) {

      const value = event.newValue.split('_');
      const action = value[0];

      const controllerThatInitiatedRedirect = value[1];

      if (controllerThatInitiatedRedirect !== components['controller'].id) {
        return;
      }

      if (redirectWindowDataWasSet(components['controller'].redirectWindowData)) {
        clearRedirectData(components['controller'].redirectWindowData);
      }

      return handleRedirectComplete().then((response) => {

        const emitter = components['controller'].paymentMethodType === 'adyen_redirect' ? adyenEmitter : clientEmitter;

        return emitter.send('redirectComplete', {
          response: response,
          action: action,
          paymentMethodType: components['controller'].paymentMethodType
        });
      });
    }
  };

}

export function createHandleSourceAuthenticationStorageEvent(components, clientEmitter, adyenEmitter) {
  return function (event) {
    if (event.key === REDIRECT_STORAGE_SOURCE_AUTHENTICATION_ID) {
      const value = event.newValue.split('_');
      const sourceAuthenticationId = value[0];

      const controllerThatInitiatedRedirect = value[1];

      if (controllerThatInitiatedRedirect !== components['controller'].id) {
        return;
      }

      if (redirectWindowDataWasSet(components['controller'].redirectWindowData)) {
        clearRedirectData(components['controller'].redirectWindowData);
      }
      adyenEmitter.send('sourceAuthenticationRedirectComplete', {
        sourceAuthenticationId: sourceAuthenticationId
      });
    }
  };

}

export function createHandleSessionAuthenticationStorageEvent(components, clientEmitter, adyenEmitter) {
  return function (event) {
    if (event.key === REDIRECT_STORAGE_SESSION_AUTHENTICATION_ID) {

      const value = event.newValue.split('_');
      const sessionAuthenticationId = value[0];

      const controllerThatInitiatedRedirect = value[1];

      if (controllerThatInitiatedRedirect !== components['controller'].id) {
        return;
      }

      if (redirectWindowDataWasSet(components['controller'].redirectWindowData)) {
        clearRedirectData(components['controller'].redirectWindowData);
      }

      adyenEmitter.send('sessionAuthenticationRedirectComplete', {
        sessionAuthenticationId: sessionAuthenticationId
      });
    }
  };

}
