export function clearRedirectData(redirectWindowData) {
  clearInterval(redirectWindowData.timer);
}

export function setRedirectWindowData(redirectWindowData, redirectWindow, determineEvent, paymentMethodType, resolve) {
  const timer = setInterval(async function () {

    const isClosed = await redirectWindowData.window.serializedWindow.isClosed();

    if (isClosed) {
      clearInterval(timer);
      determineEvent(paymentMethodType, resolve);
    }

  }, 1000);

  redirectWindowData.timer = timer;
  redirectWindowData.window = redirectWindow;
}
