import {dataChangedThatRequiresRecheck, waitForRequiredData} from '../utils';
import {attachInvoiceAttributeToDropinCheckout} from '../checkout-sessions-api';


export function invoiceAttribute(key, configuration, storage, createElement) {

  let nodeToKeep;

  const mountedVersion = {
    instance: undefined,
    data: {}
  };

  const callApiAndMount = async function () {

    if (storage.checkoutData?.payment?.session.id) {

      const customerType = storage.checkoutData.customerType;
      const paymentSessionId = storage.checkoutData?.payment?.session.id;
      const sellingEntity = storage.checkoutData?.sellingEntity?.id;

      if (mountedVersion.instance &&  dataChangedThatRequiresRecheck(storage, mountedVersion.data.customerType, mountedVersion.data.paymentSessionId, mountedVersion.data.sellingEntity)) {
        await mountedVersion.instance.destroy();
        delete mountedVersion.instance;
      }

      if (!mountedVersion.instance) {
        const instance = createElement('invoiceattribute', {
          invoiceAttribute: {
            sessionId: paymentSessionId,
            type: customerType,
          },
          hideSubmitButton: true
        });

        mountedVersion.instance = instance;
        mountedVersion.data.customerType = customerType;
        mountedVersion.data.sellingEntity = sellingEntity;
        mountedVersion.data.paymentSessionId = paymentSessionId;

        instance.on('ready', async function (event) {
          mountedVersion.ready = event;

          storage.extraData.showInvoiceAttribute = event.hasInvoices;
          storage.extraData.invoiceAttributeRequired = event.invoiceRequired;

          storage.fireClientOnChange();

        });

        await instance.mount(nodeToKeep.id);

        const errorDiv = document.createElement('div');
        errorDiv.className = 'DR-invoice-attribute-error';
        nodeToKeep.appendChild(errorDiv);

      }

    }

  };

  return {
    mount: (node) => {

      if (typeof node === 'string') {
        node = document.getElementById(node);
      }

      if (!(node instanceof HTMLElement)) {
        throw new Error('mount() requires a valid HTMLElement or id attribute.');
      }

      nodeToKeep = node;

      waitForRequiredData(storage).then( () => {
        callApiAndMount();
      });

      storage.subscribers.push(callApiAndMount);


    },
    done : async function () {

      const errorDiv = document.querySelector('.DR-invoice-attribute-error');
      errorDiv.innerHTML = '';

      try {

        const { data } = await mountedVersion.instance.submit();

        if (data.error && typeof mountedVersion.ready.invoiceRequired !== 'undefined' && mountedVersion.ready.invoiceRequired === true) {
          errorDiv.innerHTML =  storage.translations.error.notSaved;
          return false;
        } else if (mountedVersion.ready.hasInvoices === false) {
          return true;
        }

        const update = {
          invoiceAttributeId: data.id
        };

        const response = await attachInvoiceAttributeToDropinCheckout(update, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);
        if (response.status !== 200) {
          errorDiv.innerHTML =  storage.translations.error.notSaved;
          return false;
        }
        return true;
      } catch (e) {
        errorDiv.innerHTML =  storage.translations.error.notSaved;
        return false;
      }
    }

  };

}