import {
  chooseCreateSourceCatchMessage,
  formatComponentTriggerErrors,
  handleCreateSourceValidation,
  runCreateSourceAndHandleResponse
} from './controller-create-source-utils';
import {paymentServiceGetRequest} from '../../payment-service-request';
import {config} from '../config';

function isCurrencyDifferentForOptionsAndCreateSource(sourceRequest, onlineBankingOptions) {
  return sourceRequest.currency !== onlineBankingOptions.onlineBanking.currency;
}

function isCountryDifferentForOptionsAndCreateSource(sourceRequest, onlineBankingOptions) {
  try {
    return sourceRequest.owner.address.country !== onlineBankingOptions.onlineBanking.country;
  } catch(e) {
    return false;
  }
}

function isOnlineBankingNotProvided(sourceRequest) {
  return typeof sourceRequest.onlineBanking === 'undefined';
}

export function handleOnlineBankingCreateSource(componentInstance, sourceRequest, apiKey) {
  if (!componentInstance) {
    return Promise.reject('Cannot create source because all the required components have not been created for a online banking submission');
  }

  if (!componentInstance.send) {
    return Promise.reject('Cannot create source because all the required components have not been mounted for a online banking submission');
  }

  const onlineBankingPromise = componentInstance.send('getComponentData', {});
  return onlineBankingPromise.then((ibpBankCode) => {

    const triggerOnlineBanking = componentInstance.send('componentTriggerEvent', {type: 'onOnlineBankingCreateSource'});
    return triggerOnlineBanking.then((onlineBankingData) => {

      const values = [onlineBankingData];
      let errors = formatComponentTriggerErrors(values);

      if (errors.length > 0) {
        return handleCreateSourceValidation(errors);
      }

      if (isOnlineBankingNotProvided(sourceRequest)) {
        sourceRequest.onlineBanking = {};
      }

      const onlineBankingOptions = componentInstance.options;

      if (isCurrencyDifferentForOptionsAndCreateSource(sourceRequest, onlineBankingOptions)) {
        return Promise.resolve({
          error: {
            'type': 'validation_error',
            'errors' : [
              {
                'code': 'online_banking_currency_mismatch',
                'message': 'The currency specified does not match the currency provided when creating the online banking element.'
              }
            ]
          }, 
          source: null
        });
      }

      if(isCountryDifferentForOptionsAndCreateSource(sourceRequest, onlineBankingOptions)) {
        return Promise.resolve({
          error: {
            'type': 'validation_error',
            'errors' : [
              {
                'code': 'online_banking_country_mismatch',
                'message': 'The owner country specified does not match the country provided when creating the online banking element.'
              }
            ]
          },
          source: null
        });
      }

      sourceRequest.onlineBanking.bankCode = ibpBankCode.data;

      return runCreateSourceAndHandleResponse(sourceRequest, apiKey);

    });

  }).catch((error) => {
    return chooseCreateSourceCatchMessage(error);
  });
}

/**
 * runGetBanksAndHandleResponse runs create source and then handles response
 * @param apiKey
 * @param currency
 * @param country
 * @returns {Promise<T | never>}
 */
export function runGetBanksAndHandleResponse(apiKey, currency, country) {
  return paymentServiceGetRequest(apiKey, `${config.onlineBankingBanksUrl}?currency=${currency}&country=${country}`)
    .then((response) => {
      return response.data;
    })
    .catch(() => {
      return [
      ];
    });
}