import {waitForRequiredData} from '../utils';
import {attachTaxIdentifierToDropinCheckout} from '../checkout-sessions-api';


function isRequiredFieldsFilledOut(mountedVersion, storage) {
  // eslint-disable-next-line no-unused-vars
  const filledOutFields = Object.entries(mountedVersion.data).map(([key, value]) => value.type);
  return storage.checkoutData.requiredTaxIdentifiers.filter ( f => !filledOutFields.includes(f)).length === 0;
}

export function taxIdentifier(key, configuration, storage, createElement) {

  let nodeToKeep;

  const mountedVersion = {
    instance: undefined,
    data: {}
  };

  const callApiAndMount = async function () {

    if (storage.checkoutData?.payment?.session.id) {

      const customerType = storage.checkoutData.customerType;
      const paymentSessionId = storage.checkoutData?.payment?.session.id;

      if (!mountedVersion.instance) {
        const instance = createElement('taxidentifier', {
          taxIdentifier: {
            sessionId: paymentSessionId,
            type: customerType,
          }
        });
        instance.on('change', function (event) {
          if (event.complete) {
            mountedVersion.data[event.identifier.type] = {
              value: event.identifier.value,
              type: event.identifier.type,
            };
          } else {
            delete mountedVersion.data[event.identifier.type];
          }
        });

        await instance.mount(nodeToKeep.id);

        const errorDiv = document.createElement('div');
        errorDiv.className = 'DR-tax-identifier-error';
        nodeToKeep.appendChild(errorDiv);

        mountedVersion.instance = instance;

      }

    }

  };

  return {
    mount: (node) => {

      if (typeof node === 'string') {
        node = document.getElementById(node);
      }

      if (!(node instanceof HTMLElement)) {
        throw new Error('mount() requires a valid HTMLElement or id attribute.');
      }

      nodeToKeep = node;

      waitForRequiredData(storage).then( () => {
        callApiAndMount();
      });

      storage.subscribers.push(callApiAndMount);


    },
    done : async function () {

      const errorDiv = document.querySelector('.DR-tax-identifier-error');
      errorDiv.innerHTML = '';

      if (Object.keys(mountedVersion.data).length === 0 && typeof storage.checkoutData?.requiredTaxIdentifiers === 'undefined') {
        return true;
      }

      if (typeof storage.checkoutData.requiredTaxIdentifiers !== 'undefined' &&
        !isRequiredFieldsFilledOut(mountedVersion, storage)) {
        errorDiv.innerHTML =  storage.translations.error.notSaved;
        return false;
      }

      const taxIdentifiersToAdd = {
        // eslint-disable-next-line no-unused-vars
        taxIdentifiers: Object.entries(mountedVersion.data).map(([key, value]) => {
          return {
            type: value.type,
            value: value.value
          };
        })
      };

      try {
        const response = await attachTaxIdentifierToDropinCheckout(taxIdentifiersToAdd, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);
        if (response.status !== 200) {
          errorDiv.innerHTML =  storage.translations.error.notSaved;
          return false;
        }
        return true;
      } catch (e) {
        errorDiv.innerHTML =  storage.translations.error.notSaved;
        return false;
      }
    }

  };

}