import messages from './messages.json';
import supportedLocales from './supported-locales.json';

export const DEFAULT_LOCALE = 'en-US';
export const DEFAULT_COUNTRY = 'US';
export const DEFAULT_LANGUAGE = 'en';

function getLocaleMessages(locale) {
  return  messages.hasOwnProperty(locale) ? messages[locale] : undefined;

}

export function getLocaleMessage(locale, messageCode) {
  const mappedLocale = getMappedLocale(locale);
  const localizedMessages = getLocaleMessages(mappedLocale);
  return localizedMessages && localizedMessages.hasOwnProperty(messageCode) ? localizedMessages[messageCode] : undefined;
}

export function localeMessagesContainsClientProvidedLocale(locale) {
  const mappedLocale = getMappedLocale(locale);
  return Object.keys(messages).filter( key =>  key.toLowerCase() === mappedLocale.toLowerCase()).length > 0;
}

export function forceCasingOfLocale(instanceOptions) {
  const locale = instanceOptions.locale;
  const regex = /_|-/;
  const separatorIndex = locale.search(regex);
  if( separatorIndex !== -1) {
    const providedLocaleArray = locale.split(locale.charAt(separatorIndex));
    return providedLocaleArray[0] + '-' + providedLocaleArray[1].toUpperCase();
  } else {
    return DEFAULT_LOCALE;
  }

}

export function getMappedLocale(locale) {
  return supportedLocales.hasOwnProperty(locale) ? supportedLocales[locale].messageLocale : locale;
}

export function getLanguageMap() {
  return {
    ar: 'ar-EG',
    cs: 'cs-CZ',
    da: 'da-DK',
    de: 'de-DE',
    el: 'el-GR',
    en: 'en-IE',
    'en-GB': 'en-GB',
    'en-US': 'en-US',
    es: 'es-ES',
    'es-419': 'es-MX',
    et: 'et-EE',
    fi: 'fi-FI',
    fr: 'fr-FR',
    'fr-CA': 'fr-CA',
    he: 'he-IL',
    hu: 'hu-HU',
    it: 'it-IT',
    iw: 'iw-IL',
    ja: 'ja-JP',
    ko: 'ko-KR',
    // lt: 'lt-LT', Removed in CHECKOUTJS-2370
    // lv: 'lv-LV', Removed in CHECKOUTJS-2370
    nl: 'nl-NL',
    no: 'no-NO',
    pl: 'pl-PL',
    pt: 'pt-PT',
    'pt-BR': 'pt-PT',
    // ro: 'ro-RO', Removed in CHECKOUTJS-2370
    ru: 'ru-RU',
    sk: 'sk-SK',
    sl: 'sl-SI',
    // sr: 'sr-YU', Removed in CHECKOUTJS-2370
    sv: 'sv-SE',
    th: 'th-TH',
    tr: 'tr-TR',
    zh: 'zh-CN',
    'zh-HK': 'zh-HK',
    'zh-TW': 'zh-TW'
  };
}

export function getLocaleFromLanguage(language) {
  const languageIsUnderscoreFormat = /_/.test(language);
  const languageMap = getLanguageMap();
  const formattedLanguage = language.replace('_', '-');
  const locale = languageMap.hasOwnProperty(formattedLanguage.toLowerCase()) ? languageMap[formattedLanguage.toLowerCase()] : DEFAULT_LOCALE;
  return languageIsUnderscoreFormat ? locale.replace('-', '_') : locale.replace('_', '-');
}

export function getLanguageFromLocale(locale) {
  const languageMap = getLanguageMap();

  // eslint-disable-next-line no-unused-vars
  const language = Object.entries(languageMap).find(([key, value]) => {
    return value === locale.replace('_', '-');
  });

  return typeof language !== 'undefined' ? language[0].toLowerCase() : DEFAULT_LANGUAGE;
}

