    'CN': 'zh-CN',
    'CO': 'es-CO',
    'CZ': 'cs-CZ',
    'DE': 'de-DE',
    'DK': 'da-DK',
    'EC': 'es-EC',
    'EE': 'et-EE',
    'EG': 'ar-EG',
    'ES': 'es-ES',
    'FI': 'fi-FI',
    'FR': 'fr-FR',
    'GB': 'en-GB',
    'GR': 'el-GR',
    'HK': 'zh-HK',
