import { h, render, Component, Fragment }  from 'preact';

class ShippingChoiceElement extends Component {

  constructor(props) {
    super(props);

    this.state = {
    };

  }

  async componentDidMount() {
    if (this.props.shippingMethods?.length > 0 && typeof this.props.selectedShippingChoice === 'undefined') {
      this.selectShippingMethod(this.props.shippingMethods[0]);
    }
  }

  selectShippingMethod = (shippingMethod) => {
    this.props.onShippingMethodSelected(shippingMethod);
  }

  render() {
    const hasShippingMethods = this.props.shippingMethods?.length > 0;

    return (
      <div className={'DR-shipping-choice-container DR-form'}>
        <div className={'DR-shipping-choice-heading'}>{this.props.translations.misc.shippingMethodColon}</div>
        {!hasShippingMethods && <div className={'DR-no-shipping-methods'}>
          {this.props.translations.error.noAvailableShippingMethods}
        </div>}
        {hasShippingMethods && <>
          {this.props.shippingMethods.map(shippingMethod => {
            const checked = this.props.selectedShippingChoice?.description === shippingMethod.description;

            const displayAmount = new Intl.NumberFormat(this.props.locale, { style: 'currency', currency: this.props.currency }).format(shippingMethod.amount);

            return <div className={'DR-shipping-choice'}>
              <div className={'DR-shipping-choice-description'}>
                <input type="radio" id={shippingMethod.description} name="shippingChoices" checked={checked} onChange={() => this.selectShippingMethod(shippingMethod)}/>
                <label htmlFor={shippingMethod.description}>{shippingMethod.description}</label>
              </div>
              <div className={'DR-shipping-choice-amount'}>{displayAmount}</div>
            </div>;
          })}
        </>
        }
        {this.props.error && <div className={'DR-shipping-choice-error'}>{this.props.error}</div>}
      </div>
    );
  }
}

export function appendShippingChoiceElement(id, options) {
  return render(<ShippingChoiceElement {...options} />, id);
}